<template>
 <div class="actionHeader" ref="parents">
      <span>{{Textname}}</span>
      <span class="rights" v-on:click="ShutDown()">
      <Icon type="close-round"></Icon>
      </span>
      </div>
</template>
<script>

    export default {
      props:["Textname","_index"],
      data () {
            return {


            }
        },
 
	methods:{
	  ShutDown:function(){  //这个事件是来判断二级文章是否关闭的
	  if(this._index[1]===2){
	      if(this._index[0]===false||this._index[0]===true){
	         this.$store.dispatch("secondary",this._index)   //这个传递组件过来的_index的值，来判断现在是否是显示还是隐藏
	      }
	  }else if(this._index[1]===1){
	     
	        if(this._index[0]===false||this._index[0]===true){
	
	         this.$store.dispatch("seed",this._index)   //这个传递组件过来的_index的值，来判断现在是否是显示还是隐藏
	      }
	  }else if(this._index[0]===0){
	         if(this._index[0]===false||this._index[0]===true){
	
	         this.$store.dispatch("father",this._index)   //这个传递组件过来的_index的值，来判断现在是否是显示还是隐藏
	      }
	  }
             
	      
	       this.$refs.parents.parentNode.parentNode.style.display="none" //上一级目录隐藏
	      /*this.$emit("hideLink",false)
	        this.$emit("addLinks",false)
	       this.$emit("par",false)*/
	       this.$emit("Ctrl",-1)  //触发的事件；来关闭

	  }
	}
        }
</script>
<style scoped>

.actionHeader{
 background:#7dc9fb;
 font-size:14px;
 color:#fff;
 padding:10px 24px;
 overflow:hidden;
}


.actionHeader > span {
 float:left;
 display:block;
  width:auto;
  font-size:14px;
}

.actionHeader > span{

  width:auto;
}
.actionHeader .rights{
 float:right;
}

</style>



