<template>
  <div class="basis">
       <ul class="basis_scroll">
          <li v-for="(item,index) in img" v-on:click.stop="submit(index)"><img v-bind:src=img[index]></li>
         
      
       </ul>
     
  </div>
</template>

<script>

export default {
    data(){
      return{
        img:["http://images.gaga.me/photos/ymd/d15d7aea57ca9fb94a429463db73928a.jpg",
	"http://images.gaga.me/photos/ymd/0eb723a9ddc3c01585813e7e6d2ef888.jpg",
	"http://images.gaga.me/photos/ymd/95b7fd1ecf8303b73d6a22a5d7a28a05.jpeg",
	"http://images.gaga.me/photos/ymd/a99e3b411401e9694ca0fad28f44eb7f.jpeg",
	"http://images.gaga.me/photos/ymd/d5cf30e83c004f381554e89c01f7313c.jpg",
   'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1498222623914&di=924ed4c7eaf2dc1d11752634f9fa8433&imgtype=0&src=http%3A%2F%2Fpic33.nipic.com%2F20131013%2F12106414_073649149000_2.jpg'
	]
      }
    },
    methods:{
      submit:function(index){
          this.$store.dispatch("bgImg",this.img[index]) //传递到store
        }
    },
   
}
</script>

<style scoped>
.basis{
  width:100%
}
.basis ul {
  width:100%;

}
.basis_scroll{
  height:506px;
}
.basis ul li{
  width:20%;
  float:left;
  text-align: center;
  height:200px;
  text-align:center;
  margin-top:10px;
}
.basis ul li img{
  width:90%;
  height:100%;
}
</style>



