<template lang="html">
 <div class="search clearfix">

  <div class="searc-wrap" v-on:click="DateSeach(1)">
       <div class="in in1">
        <input type="text" placeholder="Sercar" v-bind:style={background:colors}>
	<Icon type="android-search" class="Icons"></Icon>
      </div>
    
    
      <div v-if="fn===1" style="width:20px;height:20px;background:rgb(125, 201, 251);color:#fff;position:absolute;right:0px;text-align:center;bottom:0px;">
	    <Icon type="checkmark-round"></Icon>
	    </div>
   </div>

 <div class="searc-wrap" v-on:click="DateSeach(2)">
      <div class="in in2">
        <input type="text" placeholder="Sercar" v-bind:style={background:colors}>
	<Icon type="android-search" class="Icons"></Icon>
      </div>
  

    <div v-if="fn===2" style="width:20px;height:20px;background:rgb(125, 201, 251);color:#fff;position:absolute;right:0px;text-align:center;bottom:0px;">
	    <Icon type="checkmark-round"></Icon>
	    </div>
</div>

<div class="searc-wrap wra2" v-on:click="DateSeach(3)">
      <div class="in in3">
        <input type="text" placeholder="Sercar" class="ic" v-bind:style={borderColor:colors}>
	<Icon type="android-search" class="Icons" v-bind:style={background:colors,border:colors}></Icon>
      </div>

   

   <div v-if="fn===3" style="width:20px;height:20px;background:rgb(125, 201, 251);color:#fff;position:absolute;right:0px;text-align:center;bottom:0px;">
	    <Icon type="checkmark-round" ></Icon>
	    </div>
</div>

<div class="searc-wrap wra3" v-on:click="DateSeach(4)">
      <div class="in in4">
        <input type="text" placeholder="Sercar" class="ic" v-bind:style={borderColor:colors}>
	<Icon type="android-search" class="Icons" v-bind:style={background:colors,border:colors}></Icon>
      </div>

     

       <div v-if="fn===4" style="width:20px;height:20px;background:rgb(125, 201, 251);color:#fff;position:absolute;right:0px;text-align:center;bottom:0px;">
	    <Icon type="checkmark-round"></Icon>
	    </div>
</div>
 
</div>
</template>

<script>
 export default {
   props:["colors"],
    data(){
        return{
	  	fn:0,
	}
    },
    methods:{
    
	DateSeach:function(Index){
		
	  this.fn = Index;
	  this.$emit("DateSeach",this.fn)
	}
    }
 }
</script>

<style scoped>


input::input-placeholder{
 color:#fff;
}
input::-webkit-input-placeholder{
 color:#fff;
}
input::-moz-input-placeholder{
 color:#fff;
}
input::-o-input-placeholder{
 color:#fff;
}
.searc-wrap{
  width:40%;
   float:left;
   margin-left:6%;
   padding:10px 40px 10px 20px;;
   position:relative;
   
}
.active{
  border:1px solid #1296db;
}

.wrapInc{
  position:absolute;
  width:24px;
  height:24px;
  border-radius:2px;
  color:#fff;
  background:#1296db;
  line-height:24px;
  text-align:center;
 right:0;
 bottom:0
}

.wra2,
.wra3{
 margin-top:20px;
}
.search{
 border:1px solid #e7e6e7;
  margin-top:10px;
  padding:20px 0;

}
.in{
   width:100%;
   position:relative;
  
   
}
.in input{
  width:100%;
  height:31px;
  padding:4px 20px;
  border-radius:24px;
  border:0;
  background:#1296db;
  color:#fff;
}

.in1 input{
  border-radius:2px;
}
.in3 input {
 background:none;
 border:1px solid #1296db;
 color:#1296db;
 border-radius:0;
 outline:none;


}


input.ic::input-placeholder{
 color:#1296db;
}
input.ic::-webkit-input-placeholder{
 color:#1296db;
}
input.ic::-moz-input-placeholder{
 color:#1296db;
}
.input.ic::-o-input-placeholder{
 color:#1296db;
}
input.ic::-ms-input-placeholder{
 color:#1296db;
}

.in4 input{
 background:none;
  border:1px solid #1296db;
  border-radius:12px;
  color:#1296db;
}

input:focus { outline: none; } 

.Icons{
   position:absolute;
   right:10px;
   top:8px;
   color:#fff;

}
.in3 > .Icons{
top:0px;
  width:31px;
  right:0;
  height:31px;
  background:#1296db;
  text-align:center;
  line-height:31px;
}

.in4 > .Icons {
  width:24px;
  height:24px;
  border-radius:24px;
  background:#1296db;
  color:#fff;
  text-align:center;
  line-height:24px;
  right:4px;
  top:3px;
  
  
}
</style>



