<template>
    <div class="artiList">
   <div class="artiList_name" v-text="setvalue.name">文章列表</div>
   <a href="#" v-for="item in articeList" v-text=item></a>
   </div>
</template>

<script>
export default {
data(){
  return{
  }
},
computed:{
     	articeList(){
     		 return this.$store.state.moduleElement.articeList;
     	},
     	setvalue(){
     		 return this.$store.state.moduleElement.setvalue;
     	}
     }
  
}
</script>

<style scoped>


.artiList{
  width:100%;
  height:auto;
  overflow:hidden;
}
.artiList_name{
	background:#1296db;
	padding:6px;
	color:#fff;
	
}
.artiList a{
	display: block;
	padding:6px;
	background:#F2F2F2;
	margin-top:2px;
}
</style>



