
<template>
<div classs="add" style="position:absolute;right:20px;margin-top:20px;">
    <a type="primary" style="font-size:14px;" @click="modal1 = true"><Icon type="plus-round"></Icon><span style="margin-left:8px;">添加栏目</span></a>
    <Modal
        v-model="modal1"
        title="添加栏目"
        @on-ok="ok"
        @on-cancel="cancel">
        <div class="addName">
	<div style="display:inline-block;width:30px;">标签 :</div>                           
	 <input type="text" style="padding:4px 8px;margin-left:10px;" v-bind:value="NameText" v-model="NameText">
	 
	</div>
	 <div class="addRiao" style="margin-top:20px;">
<div class="addLable">	 
   <div style="display:inline-block;width:30px;">标签 :</div>
     <span style="margin-left:10px;">
	<Radio-group v-model="disabledGroup">
        <Radio label="默认"></Radio>
        <Radio label="隐藏"></Radio>
        <Radio label="自定义 ( 最佳尺寸80*80 )"></Radio>
	</Radio-group>
    </span>
</div>

<div class="addZp" style="overflow:hidden;margin-left:40px;margin-top:20px;" v-show="disabledGroup.length > 2">
     
      <span  style="padding:4px;color:#7dbfbb;font-size:30px;float:left;text-align:center;line-height:0;border:1px solid #7dc9fb;width:50px;border-radius:4px;">
       <div style="background:#a3d8d6;border-radius:4px;">
       <Icon type="camera"></Icon>
       </div>
       </span>

      <span style="float:left;margin-left:8px;margin-top:4px;">
         <Upload
        multiple
        action="#">
        <Button type="ghost" style="border:1px solid #7dc9fb;color:#7dc9fb">系统图标</Button>
    </Upload>
      </span>
      <span style="float:left;margin-left:8px;margin-top:4px;">
            <Upload
        multiple
        action="#">
        <Button type="ghost" style="border:1px solid #7dc9fb;color:#7dc9fb">上传文件</Button>
    </Upload>
      </span>
</div>

<div class="addLable" style="margin-top:20px;">	 
   <div style="display:inline-block;width:30px;">标签 :</div>
     <span style="margin-left:10px;">
	<Radio-group v-model="disabledGroText">
        <Radio label="默认"></Radio>
        <Radio label="自定义"></Radio>
        <Radio label="无"></Radio>
	</Radio-group>
    </span>


<div class="addRul" style="margin-left:40px;margin-top:20px;" v-show="disabledGroText.length>2">
  <Input v-model="Url" placeholder="请输入..." style="width: 300px"></Input><Button  style="border:1px solid #dbd8d9;color:#000;border-raidus:0;background:#fff;margin-left:6px;">设置连接</Button>
</div>

</div>

	 </div>
    </Modal>
    </div>
</template>
<script>
    export default {
        data () {
            return {
	      
                modal1: false,
		disabledSingle: true,
                disabledGroup: "",
		disabledSh:false,
		disabledGroText: '',
		NameText:"默认栏目",
		Url:"",
		TextJson:{}
		
            }
        },
        methods: {
            ok () {
	        this.TextJson.Name = this.NameText
		this.TextJson.GroText  = this.disabledGroText
		this.TextJson.Group  = this.disabledGroup
		this.TextJson.Url  = this.Url
		console.log(this.TextJson)
                this.$Message.info('添加成功');
            },
            cancel () {
                this.$Message.info('取消');
            }
	   
        }
    }
</script>

<style>
.ivu-modal-content .ivu-modal-header{
background:#7dc9fb;

}

.ivu-modal-header-inner{
color:#fff;
}
 .ivu-modal-close .ivu-icon-ios-close-empty{
 color:#fff;
 }
</style>


