<template>

 <div class="Product">
   <div class="headers">
<span v-text="Settings.name"></span>
<span><Icon type="ios-keypad"></Icon></span>
   </div>
<!--产品展示第一种状态 根据 store传递的产品，来显示的状态-->
     <div class="ProductN" v-if="Settings.Index===1">
    <a  class="ProList" v-for="(item,index) in product">
      <img src="http://dl.bizhi.sogou.com/images/1152x864/2013/08/05/355306.jpg">
      <span v-if="Settings.text!='否'">{{item}}</span>
      <span class="names">
<i class="left">$280.00</i>
<i class="right"><Icon type="ios-heart-outline"></Icon></i>
      </span>

    </a>

     </div>

  <div class="ProductT" v-if="Settings.Index===2">

         <a class="ProList" v-for="(item,index) in product">
             <div class="left"><img src="http://dl.bizhi.sogou.com/images/1152x864/2013/08/05/355306.jpg"></div>
             <div class="right">
               <span v-if="Settings.text!='否'">{{item}}</span>
               <span class="names">
         <i>$280.00</i>
         <i><Icon type="ios-heart-outline"></Icon></i>
               </span>
             </div>
         </a>


  </div>
 </div>
</template>

<script>

export default {
data(){
  return {
   
  }
},
  methods:{
 
  },
  computed:{
  	 product(){
  	 
  	 	return this.$store.state.moduleElement.product
  	 
  	 },
  	 //获取设置数据的
  	 Settings(){
  	 
  	 	return this.$store.state.moduleElement.Settings
  	 
  	 }
  }

}
</script>

<style scoped>
.headers{
  color:#fff;
  padding:6px;
  padding-left:2%;
  font-size:14px;
  background:rgb(176,14,56);
  overflow: hidden;
}
.headers span:first-child{
  float:left
}
.headers span:last-child{
  float:right
}
.ProductT,
.ProductN{
  width:100%;
  overflow: hidden;
  background:rgb(243,243,243)
}

.ProductN .ProList{
  padding:10px;
  float:left;
  width:48%;
  margin-left:1%;
  margin-right:1%;
  text-align: center;
  background:#fff;
    margin-bottom:10px;
}


.ProductN .ProList img{
  display: block;
  width:100%;
}
.ProductN .ProList  span{
  display: block;
  width: 100%;
  margin-top:10px;
}
.ProductN .ProList .names{
border-top: 1px solid #666;
padding:10px 0;
overflow: hidden;
}
.ProductN .ProList .names .left{
  float:left;
  color:red

}
.ProList .names .right{
  line-height:20px;
  float:right;

}

/*第二种样式*/
.ProductT .ProList{
  margin-bottom:10px;
  padding:10px;
  overflow: hidden;
  display:block;
    background:#fff;
}
.ProductT .ProList .left{
  width:46%;
  float:left;
}
.ProductT .ProList .left img{
  width:100%;
}
.ProductT .ProList .right{
    width:46%;
  float: right;
}
.ProductT .ProList .right span{
  display: block;
  overflow: hidden;
  margin-top:10px;
}
.ProductT .ProList .right span > i:nth-child(1){
  float:left;
  color:red;
}
.ProductT .ProList .right span > i:nth-child(2){
  float:right;
}
</style>



