<template lang="html">
<div class="RightWrap">
<div class="RightSide">
    <!--右侧-->
   <div class="RightSide_top">
     <span v-bind:class="fn===false? 'active':''" v-on:click="Active(1)">模块选择</span>
     <span v-bind:class="fn===true? 'active':''" v-on:click="Active(0)">栏目</span>
   </div>

    <Tabs v-show="fn===false" v-on:click="Active(1)">
            <Tab-pane label="风格主题">
              <basisColor></basisColor>

            </Tab-pane>
            <Tab-pane label="顶部导航编辑">顶部导航编辑</Tab-pane>
            <Tab-pane label="轮播广告图">轮播广告图</Tab-pane>
        </Tabs>
	<column v-show="fn===true"></column>
</div>

<AddDirectory></AddDirectory>
</div>
</template>

<script>
import basisColor from "../PublicModule/basisColor"
import column from "./RightChild/column"
import AddDirectory from "./RightChild/AddDirectory"
export default {
  data(){
    return {
       ClassNames:"active",
       fn:true
    }
  },
  methods:{
     Active:function(fn){
     if(fn===1){
           this.fn = false
         }else{
           this.fn = true
         }
}
     },
     components:{
      basisColor,
      column,
      AddDirectory
     }


}
</script>

<style lang="css">
.RightWrap{
width:600px;
 float:right;
 position:relative;
}
.RightSide{
  
 width:600px;
  border:1px solid #d2d2d2;
}
.RightSide_top{
  width:100%;
  background:#0099ff
}
.RightSide_top span {
  width:24%;
  font-size:14px;
  text-align: center;
  color: #fff;
  display: inline-block;
  padding:12px 0;
}

.RightSide_top .active{
  color:#0099ff;
  background:#fff
}
</style>



