<template>

 <div class="Product">
    <modelName :Textname="Textname[0]" v-on:Ctrl="Ctrl" :_index=[false,0]></modelName>
     <div class="twoactive">
     <span v-on:click="switchsBan(true)" v-bind:class="[rowls?'active':'']">常规</span><span v-on:click="switchsBan(false)" v-bind:class="[rowls!=true?'active':'']">高级</span></div>
   <div class="conventional" v-show="rowls!=false">
      <NameModel v-on:accept="accept"></NameModel>
      <!--样式打钩-->
      <div class="style">
        <span>样式 : </span>
	<div class="foucrs">
	   <div v-on:click="Settings.Index=1" v-bind:class="{'par':Settings.Index===1}"><img src="../../assets/images/2.png">
	     <Icon v-show="Settings.Index===1" type="checkmark-round"  v-bind:class="{'selecte':Settings.Index===1}"></Icon>
	   </div>
	   <div v-on:click="Settings.Index=2" v-bind:class="{'par':Settings.Index===2}">
	   <img src="../../assets/images/3.png">
	   <Icon v-show="Settings.Index===2" type="checkmark-round" v-bind:class="{'selecte':Settings.Index===2}"></Icon></div>
	</div>
      </div>

    <!--选择产品-->
    <div class="spanText">
      <span>选择产品 : </span>
	   <div class="categorflst">

    <div class="labels">
	     <Radio-group v-model="animal">
                <Radio label="选择指定产品"></Radio>
                <Radio label="选择产品分类"></Radio>

              </Radio-group>
	       </div>
	       </div>
    </div>



    <!--搜索-->

    <div class="large" v-show="animal===animal1">
       <Input icon="search" placeholder="请输入..."  v-model="query" style="width: 200px"></Input>

      <div class="add">
        <div class="addLeft" >

	  <div id="staggered-list-demo">

  <transition-group
    name="staggered-fade"
    tag="ul"
    v-bind:css="false"
    v-on:before-enter="beforeEnter"
    v-on:enter="enter"
    v-on:leave="leave"
  
  >
    <li
      v-for="(item, index) in computedList"
      v-bind:key="item.msg"
      v-bind:data-index="index"
      v-on:click="replace(index)"
    >{{ item.msg }}</li>
  </transition-group>
</div>
 <div class="addcanp" @click="modal1 = true"><Icon type="plus-round"></Icon>添加产品</div>


 <!--添加按钮弹出层-->
 <Modal
        v-model="modal1"
        title="添加产品"
        @on-ok="ok"
        @on-cancel="cancel">
        <Input v-model="value" placeholder="请输入..." style="margin:20px 14px;width: 200px"></Input>
    </Modal>

	</div>
	
	
	 <div class="addcenter">
	   <Icon type="play"></Icon>
	 </div>
	 <div class="addright">
	   <div v-for="(item,index) in rightdata" class="hdie">
	     <div class="column_list">{{item}}</div>

		 <div class="column_list column_ico">
		    <span v-on:click="Movedown(index)"><Icon type="arrow-up-c"   v-show="index!=0" style="color:#c7dae4"></Icon></span>
                     <span v-on:click="Moveup(index)">
		     <Icon type="arrow-down-c" v-show="index!=rightdata.length-1" style="color:#7dc9fb"></Icon>
		     </span>

		     <span v-on:click="Movedelete(true,index)"><Icon type="close" style="color:#c7dae4"></Icon></span>
		 </div>


	   </div>
	 </div>
      </div>
    </div>


  <div class="listLable" v-show="animal===animal2">
     <Checkbox-group v-model="fruit">
       <Checkbox :label=item.bigclassname v-for="item in ProData"></Checkbox>
        
    </Checkbox-group>
      <div class="catText">产品展示将最多显示10个产品，其余的产品将在产品列表页中显示</div>
  </div>

   </div>

   <!--编辑图片-->
  <div class="senior" v-show="rowls!=true">
       <div class="basis">
          <h2>基础设置</h2>
	   <div class="basischild">
	   <span style="float:left;margin-right:14px;margin-left:28px;">显示产品 :</span>
	   <input type="text" class="batext" v-model="Settings.indexs"><i>个</i>
	   </div>

           <div class="seniorleft">
	   <span class="left" style="margin-left:28px;">显示更多 : </span>
	   <div class="right">
	   <div>
	      <Radio-group v-model="Settings.animals">
               <Radio label="是"></Radio>
                <Radio label="否" style="margin-left:20px;"></Radio>
                </Radio-group>
		</div>
    
		</div>
	   </div>
	   <!--文字设置-->
        <div class="seniorleft">
	   <h2>文字设置 :</h2>
	   <div class="basischild">
	    <span class="left">显示产品名称 : </span>
	    <Radio-group v-model="Settings.text">
               <Radio label="是"></Radio>
                <Radio label="否" style="margin-left:20px;"></Radio>
                </Radio-group>
	   </div>
	</div>

<!--跳转设置-->
<div class="seniorleft">
 <h2>跳转设置 :</h2>
  <div class="basischild">
	   <span class="left">点击图片跳转 : </span>
	   <div class="right">
	   <div>
	      <Radio-group v-model="rul">
               <Radio label="产品链接页面"></Radio>
                <Radio label="自定义" style="margin-left:20px;"></Radio>
                </Radio-group>
		</div>
             <div class="right_Mar" v-if="rul=='自定义'">
	       <Input v-model="Settings.jump" placeholder="填入跳转的页面" style="width: 300px"></Input>
	        <Button type="ghost" v-on:click="addLink()">设置连接</Button>
	     </div>
		</div>
	   </div>
</div>
       </div>
  </div>
   <div class="Button">
      <Button type="text" v-on:click.stop="submit()">确定</Button>
       <Button type="text" v-on:click="Ctrl">取消</Button>
  </div>
  
  
  <!--设置链接-->
<div class="setlink" v-show=secondary>

 <articlelinks></articlelinks>

</div>
  
 </div>

<!--编辑图片-->


</template>

<script>
var exchange;
var In=0;
import modelName from "../PublicModule/modelName"
import NameModel from "../PublicModule/NameModel"
import articlelinks from "../PublicModule/articlelinks"
export default {
   data(){
     return {
      query: '',
    list: [
     { msg: 'Bruce Lee' },
      { msg: 'Jackie Chan' },
      { msg: 'Chuck Norris' },
      { msg: 'Jet Li' },
      { msg: 'Kung Fury' }
    ],
     
   show:true,
   rowls:true,
    Textname:["产品展示模块","编辑图片"],
	modal1: false,
	fruit:[],
	value:"",
    HeaderName:"",
	
    rul:"产品链接页面",
	temporary:[],
	arrayData:[],
	animal:"选择指定产品",
	animal1:"选择指定产品",
	animal2:"选择产品分类",
	rightdata:[],
    Settings:{name:"",indexs:"",animals:"否",text:"是",jump:"",Index:1,},
	
     }
   },
      created(){
   	  this.$store.dispatch("ProData")   // 页面加载时候获取产品分类数据
   },

   methods:{
  /*这个事件是由NameModel来跟输入的数据模块触发*/
   accept:function(data){
        this.Settings.name = data  
	
      },
   ok () {
        this.list.push({msg:this.value})
        this.$Message.info('添加成功')
		this.value="";
            },
   cancel () {
   this.$Message.info('点击了取消');
   },
   
  beforeEnter: function (el) {
      el.style.opacity = 0
      el.style.height = 0
    },
    enter: function (el, done) {
      var delay = el.dataset.index * 150
      setTimeout(function () {
        Velocity(
          el,
          { opacity: 1, height: '1.6em' },
          { complete: done }
        )
      }, delay)
    },
    leave: function (el, done) {
      var delay = el.dataset.index * 150
      setTimeout(function () {
        Velocity(
          el,
          { opacity: 0, height: 0 },
          { complete: done }
        )
      }, delay)
    },

	   switchs:function(fn){
	     this.show = fn;
	   },
     switchsBan:function(fn){
	      this.rowls = fn
	    },

      
      Movedown:function(Index){

         exchange = this.rightdata[Index];
	 this.rightdata[Index] = this.rightdata[Index-1];
	 this.rightdata[Index-1] = exchange
this.$set(this.rightdata,Index,this.rightdata[Index])
      },
      
      Moveup:function(Index){
          exchange = this.rightdata[Index];
	 this.rightdata[Index] = this.rightdata[Index+1];
	 this.rightdata[Index+1] = exchange
     this.$set(this.rightdata,Index,this.rightdata[Index]) // 由于数组不能实时更新我们要用set来更新
      },
      
      Movedelete:function(nodesc,Index){
         this.rightdata.splice(Index,1)
         this.$Notice.info({
                    title: '删除成功',
                    desc: nodesc ? '' : '成功删除'
                });
      },
      replace:function(Index){
         this.rightdata.push(this.list[Index].msg)
      },
     
    Ctrl:function(){
         this.$emit("on",-1)
      },
      
   //传递的参数来改变二级状态
      addLink:function(){
        if(this.secondary===false){
	      this.$store.dispatch("secondary",[true,2])
	   }else{
	      this.$store.dispatch("secondary",[false,2])
	   }
      },   
      
  submit:function(){
  	
  	  this.$store.dispatch("Settings",this.Settings)
  	  
  	if(this.animal===this.animal1){
  		 this.$store.dispatch("product",this.rightdata)
  	}else{
  		 this.$store.dispatch("product",this.fruit)
  	}
  	
   if(In===0){
	  this.$root.eventHub.$emit("Module","Product")
	  In++;
	}
      this.Ctrl();
	
      }

	},
computed:{
    computedList: function () {
        var vm = this
        return this.list.filter(function (item) {
        return item.msg.toLowerCase().indexOf(vm.query.toLowerCase()) !== -1
      })
    },
    ProData:function(){
    return this.$store.state.CateGory.ProData   //获取产品分类数据
   
    },
     // 获取二级状态值
     secondary(){
        return this.$store.state.CateGory.secondary
      },
      // 拼接的连接
     connection(){
        return this.$store.state.CateGory.connection
       }
     },
       watch:{    //这里监听 connection是否改变了，如果改变就会复制给jump然后渲染到设置连接文本框
       connection:function(){
         
        this.Settings.jump = this.connection
	   // this.gather.url =  this.connection  //获取当前的连接地址
       }
       },
   components:{
    modelName,
    NameModel,
      articlelinks 
   },
 

}
</script>

<style scoped>
	.setlink{
width:240px;
position:absolute;
top:0px;
right:-270px;
}
/*设置连接元素*/
	
i{
  font-style:normal;
}
.pictures,
.Product{
  width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;


}
.twoactive{
  padding:0 24px;
  background:#e6e6e6;
  font-size:12px;
}
.twoactive > span{
  height:100%;
  padding:0 8px;
  display:inline-block;
}
.twoactive .active{
  background:#fff;
  color:#565656;
}

.conventional{
 padding:0 24px;
 margin-top:20px;
}

.style{
 font-size:14px;
 overflow:hidden;
 margin-top:20px;
}
.style span{
 float:left;
 line-height:40px;
}
.foucrs{
  float:left;
  overflow:hidden;
}

.foucrs div {
 float:left;
 margin-left:14px;
 margin-right:14px;
 padding:10px;

 position:relative
}
.foucrs .par{
   border:1px solid rgb(101,154,86);
}
.foucrs div .selecte{
  width:16px;
  height:16px;
  background:rgb(101,154,86);
  color:#fff;
  line-height:16px;
  text-align:center;
  border-radius:2px;
  position:absolute;
  right:0;
  bottom:0;
}
.spanText{
 margin-top:20px;
 overflow:hidden;
}
.spanText span {
margin-right:14px;
 font-size:14px;
 float:left;
}
.right_Mar,
.large{
 margin-top:20px;
}
.add{
 width:100%;
 overflow:hidden;
 margin-top:20px;
}
.addLeft{
width:45%;
float:left;
border:1px solid rgb(126,201,255);
height:200px;
font-size:14px;
padding:10px;
position:relative;
padding-bottom:45px;
}
.addcenter{
 width:10%;
 float:left;
 height:200px;
color:rgb(171,191,218);
font-size:28px;
text-align:center;
line-height:200px;
}
.addright{
 width:45%;
 float:left;
 height:200px;
border:1px solid rgb(126,201,255);
font-size:14px;
padding:10px;
overflow-Y:scroll;

}
.hdie{
 overflow:hidden
}
.column_list{
 float:left;
}
.column_ico{
 float:right;
}
.addcanp{
 text-align:center;
 border:1px dashed rgb(207,209,208);
 padding:4px 0;
 position:absolute;
 bottom:10px;
 width:80%;
 left:10%;
}
.listLable{

}
.ivu-checkbox-group-item{
  width:12%;
  margin:0px;
  margin-top:20px;

  text-align:center;

}
.catText{
    margin-top:10px;
    background:#ffb399;
    color:#fff;
    display:inline-block;
    padding:2px 10px;
    font-size:14px;
}
.Button{
 position:absolute;
 right:20px;
 bottom:10px;
}

.senior{
 padding:0 24px;
margin-top:20px;
  font-size:14px;
 }
 .senior h2{
   font-size:16px;
 }
 .Hpad{
 padding:0;
 height:0
 }
 .seniorleft{
  overflow:hidden;
  margin-top:20px;
 }
 .seniorleft .left{
  float:left;
  margin-right:14px;
 }
 .seniorleft .right{
  float:left;
 }

 .batext{
   display: inline-block;
    width: 30px;;
    height: 24px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 12px;
    border: 1px solid #dddee1;
    border-radius: 4px;
    color: #495060;
    background-color: #fff;
    background-image: none;
    position: relative;
    cursor: text;
    margin-right:14px;
    transition: border .2s ease-in-out,background .2s ease-in-out,box-shadow .2s ease-in-out;
 }
 .basischild{
  margin-top:20px;
  overflow:hidden;
 }

.Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
#staggered-list-demo{
height:130px;
  overflow-Y:scroll;
}
</style>



