<template lang="html">
  <div class="gallery">

  	<div class="gall_img">
  	<span v-for="item in SystemImg"><img :src="'http://image.sonhoo.com/'+item.Pic" v-on:click ="addImg(item.Pic)"></span>
  	 </div>
  	 <div class="page">
  	 	<Page :total="100" show-sizer></Page>
  	 </div>
  </div>
</template>

<script>
 export default {
  props:["SystemImg"],
  methods:{
  	addImg:function(url){
//把连接地址返回给父级，父级在添加图片

  		this.$emit("addImg","http://image.sonhoo.com/"+url) 
  		
  	}
  },
 }
</script>

<style scoped>
.gallery{
	width:500px;
	height:430px;
	overflow: hidden;
	padding-bottom:40px;
	position: relative;
}
.gallery .gall_img span{
	 width:64px;
	 height:64px;
	 float:left;
	 padding:4px 4px;
	display: block;
}
.gallery .gall_img span img{
	width:100%;
	height:100%;
}
.gallery .page{
	width:100%;
	position: absolute;
	bottom:0px;
	
}
.gallery .page ul{
	display: inline-block;

}
</style>



