<template lang="html">
  <div class="announcement">
  	<div class="name">{{announcement.name}}</div>
    <div class="incon_realtive">
     <i class="iconfont" v-bind:class="announcement.icon.icon" v-bind:style="{color:announcement.icon.color}"></i>
     <div class="marquee"><marquee v-bind:direction="announcement.dire" scrollamount="announcement.speed">
     	<a  v-for="item in announcement.Data" v-text="item.name" v-bind:href="item.url"></a>
     </marquee></div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      colors:"red"
    }
  },
  computed:{
  	 announcement(){
  	console.log(this.$store.state.moduleElement.announcement)
  	 	return this.$store.state.moduleElement.announcement
  	 
  	 }
  	}
}
</script>

<style scoped>
	.announcement{
		width:100%;
		padding:2%;
		background:#fff;
		
	}
	.name{
		color:#fff;
		 padding:4px;
  padding-left:2%;
  font-size:14px;
  background:rgb(176,14,56);

	}
.incon_realtive{
	position: relative;
}
.iconfont{
	position: absolute;
	
	top:0px;
}
.marquee{
	width:90%;
	margin:0 auto;
}
.marquee marquee{
	display: block;
	padding: 2% 0;;
	}
</style>



