<template>
    <div class="edit">
      <modelName :Textname="Textname"></modelName>
      <div class="twoactive">
     <span v-bind:class="[rowls?'active':'']">常规</span></div>
    <div class="editmodel">
       <span class="left">模板样式 :</span><styleColor></styleColor>
      </div>

<!--排版样式-->
      <div class="style">
        <span>排版样式 : </span>
	<div class="foucrs">
	   <div><img src="../../assets/images/2.png">
	     <Icon type="checkmark-round" class="selecte"></Icon>
	   </div>
	   <div><img src="../../assets/images/3.png"><Icon type="checkmark-round" class="selecte"></Icon></div>
	</div>
      </div>
<!--文章图片-->
<div class="textImg">
  <span>文章图片 : </span>
   <div>
  
     <Upload action="">
        <Button type="ghost" icon="ios-cloud-upload-outline">上传图片</Button>
    </Upload>
  </div>
</div>

<!--页面元素-->
<div class="pageElement">
  <span>页面元素 : </span>
   <div>
  
      <Checkbox-group v-model="fruit">
        <Checkbox label="作者"></Checkbox>
        <Checkbox label="日期"></Checkbox>
        <Checkbox label="来源"></Checkbox>
	 <Checkbox label="网址"></Checkbox>
        <Checkbox label="首页"></Checkbox>
	 <Checkbox label="上/下一篇"></Checkbox>
       
    </Checkbox-group>
  </div>

</div>
 <div class="Button">
     <Button type="text">确定</Button>
       <Button type="text">取消</Button>
  </div>
    </div>
</template>
<script>
import modelName from "../PublicModule/modelName"
import NameModel from "../PublicModule/NameModel"
import styleColor from "../PublicModule/styleColor"
    export default {
      data () {
            return {
	       
		show:true,
		rowls:true,
		 fruit: ['苹果'],
		Textname:"编辑文章详情模块"
		}
		
        },
	 methods:{
	   switchs:function(fn){
	     this.show = fn;
	   },
            switchsBan:function(fn){
	      this.rowls = fn
	    }
},
	 components:{
	 modelName,
	 styleColor
	}
    }
</script>

<style scoped>
.edit{
 width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;
}
.editmodel{
 padding:0 24px;
 overflow:hidden;
 margin-top:20px;
}
.editmodel .left{
 float:left;
 font-size:14px;
 margin-right:10px;
}

.style{
 font-size:14px;
 overflow:hidden;
 margin-top:20px;
 padding:0 24px;
}
.style span{
 float:left;
 line-height:40px;
}

.foucrs{
  float:left;
  overflow:hidden;
}

.foucrs div {
 float:left;
 margin-left:14px;
 margin-right:14px;
 padding:10px;
 border:1px solid rgb(101,154,86);;
 position:relative
}
.foucrs div .selecte{
  width:16px;
  height:16px;
  background:rgb(101,154,86);
  color:#fff;
  line-height:16px;
  text-align:center;
  border-radius:2px;
  position:absolute;
  right:0;
  bottom:0;
}
.pageElement,
.textImg{
overflow:hidden;
font-size:14px;
padding:0 24px;
margin-top:20px;
}

.textImg > span{
 float:left;
 margin-right:14px;
 line-height:32px;
}
.pageElement > span{
 float:left;
 margin-right:14px;
}
.pageElement > div,
.textImg > div{
 float:left;
}

 .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}

</style>


