<template>
  <!--分类导航-->
   <div class="ifica_action">
   	 <div v-show="seed!=true">
    <modelName v-bind:Textname="Textname[0]" v-on:Ctrl="Ctrl" :_index=[false,0]></modelName>
      <!--常规-->
  <div class="twoactive">
 <span v-on:click="switchsBan(true)" v-bind:class="[rowls?'active':'']">常规</span><span v-on:click="switchsBan(false)" v-bind:class="[rowls!=true?'active':'']">高级</span>
     </div>
<!--切换-->
<div class="twolable" v-show="rowls!=false">
 <div class="twoLableColor clearfix">
    <span>模板样式 :</span>
     <styleColor v-on:Module="btnColor"></styleColor>
    
      <searchs v-on:DateSeach="DateSeach" v-bind:colors="colors"></searchs>
    </div>
 
</div>
<div class="senior" v-show="rowls!=true">4</div>


    <div class="Button">
     <Button type="text" v-on:click.stop="submit">确定</Button>
       <Button type="text" v-on:click="Ctrl">取消</Button>
  </div>
   </div>


</div>
    </div>
</template>
<script>
var In=0
import modelName from "../PublicModule/modelName"
import searchs from './input/search'
import styleColor from '../PublicModule/styleColor'
    export default {
      data () {
            return {
        value: '',
		show:true,
		rowls:true,
		Textname:["产品搜索"],
		colors:"",
		indexColor:{
			colors:"",
			indexs:0
		}
            }
        },
	methods:{
		  btnColor:function(tar){
         
          this.colors = tar;
          
       
       },
	   switchs:function(fn){
	     this.show = fn;
	   },
            switchsBan:function(fn){
	      this.rowls = fn
	    },
	    DateSeach:function(Index){
	    
	      this.indexColor.indexs = Index
	    },
    Ctrl:function(){

           this.$emit("on",-1)
      },
           addOr:function(Index){
          // 注意： Index 是用来判断当前是添加新的内容，还是修改当前点击的内容（Index代表当前点击的下标）
            if(this.seed===false){
	      this.$store.dispatch("seed",[true,1,Index])
	   }else{
	      this.$store.dispatch("seed",[false,2,Index])
	   }
      },
       submit:function(){
       	 this.indexColor.colors = this.colors
       	 this.$store.dispatch("search",this.indexColor)
         if(In===0){
	    this.$root.eventHub.$emit("Module","searchs")
	    In++;
	}
     
        this.Ctrl();
      }
	},
	  computed:{
         // 获取一级状态值
     seed(){
        return this.$store.state.CateGory.seed
      },
      seed_index(){
        return this.$store.state.CateGory.seed_index
      },
  },
	components:{
	 searchs,
	 styleColor,
	 modelName
	}
  }
</script>
<style scoped>

.ifica_action{
 width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;
  
}
.actionHeader{
 background:#7dc9fb;
 font-size:14px;
 color:#fff;
 padding:10px 24px;
 overflow:hidden;
}



.twoLableColor > span {
 float:left;
 display:block;
}

.actionHeader > span{
 
  width:auto;
}
.rights{
 float:right;
}

.twoactive{
  padding:0 24px;
  background:#e6e6e6;
  font-size:12px;
}
.twoactive > span{
  height:100%;
  padding:0 8px;
  display:inline-block;
}

.senior,
.twolable,
.twolable2,
.twolable1{
margin-top:30px;
padding:0 24px;
 font-size:14px;
 position:relative;
 padding-bottom:60px;
}
.twolable1 > div > span{
  margin-right:10px;
}
.over{
margin-top:30px;
 overflow:hidden;
}
.over > span{
 float:left;
 
 
}
.over div{
width:80%;
 float:left;
 overflow:hidden;
}
.over div span{
  padding:2px 4px;
  color:#fff;
  display:block;
  float:left;
  background:#ffb399;
  margin-right:20px;
  margin-bottom:10px;
}

.Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
.ivu-btn-dashed{
 border:0;
}
.active{
  background:#fff;
  color:#565656;
}

</style>


