<template>
  <div class="Left_Module">
   <div class="list_switch">
   <span v-bind:class="fn===false? 'active':''" v-on:click="Active(1)">模块选择</span>
   <span v-bind:class="fn===true? 'active':''" v-on:click="Active(0)">已使用</span>

   </div>
   <PubiceLeft v-show="fn===false" v-on:click="Active(1)"></PubiceLeft>
   
  </div>
</template>

<script>
import PubiceLeft from "../PublicModule/PubiceLeft"
export default {
   data(){
     return {
        ClassNames:"active",
        fn:true
     }
   },
   methods:{
      Active:function(fn){
      if(fn===1){
            this.fn = false
          }else{
            this.fn = true
          }

      },
     

   },
   components:{
    PubiceLeft
   }, 
  

}
</script>

<style scoped>
.Left_Module{
  width:320px;
  border:1px solid #d2d2d2;
  font-size:0px;
  padding-bottom: 40px;
  float:left;

}
.Left_Module  span{
  width:50%;
  font-size:14px;
  text-align: center;
  color: #fff;
  display: inline-block;
  padding:12px 0;

}
.list_switch{
  background:#0099ff
}
.list_switch .active{
  color:#0099ff;
  background:#fff
}
</style>



