<template>
<div class="Addarticle">
 <modelName :Textname="Textname"></modelName>
 <div class="addNamePar">
  <div class="addName">
    <span>标题 :</span><Input placeholder="请输入..." style="width: 200px"></Input>
  </div>
   <div class="categorName">
	    <span>作者 :</span><Input  placeholder="请输入..." style="width: 200px;"></Input>
	      <span class="location"><Checkbox >隐藏</Checkbox></span>
	 </div>    

  <div class="categorName">
	    <span>网址 :</span><Input  placeholder="请输入..." style="width: 200px;"></Input>
	      <span class="location"><Checkbox >隐藏</Checkbox></span>
	 </div>    

  <div class="addName">
     <span>分类 :</span><Select v-model="model3" style="width:100px">
        <Option v-for="item in cityList" :value="item.value" :key="item">{{ item.label }}</Option>
    </Select>
  </div>
</div>
    </div>
</template>
<script>
import modelName from "../PublicModule/modelName"

    export default {
      data () {
            return {
              name:"",
	      author:"",
	       url:"",
	       ification:"",
	       Json:[],
               Textname:"添加文章",
	        cityList: [
                    {
                        value: 'beijing',
                        label: '北京市'
                    },
                    {
                        value: 'shanghai',
                        label: '上海市'
                    },
                    {
                        value: 'shenzhen',
                        label: '深圳市'
                    },
                    {
                        value: 'hangzhou',
                        label: '杭州市'
                    },
                    {
                        value: 'nanjing',
                        label: '南京市'
                    },
                    {
                        value: 'chongqing',
                        label: '重庆市'
                    }
                ],
               model3: '',
             
            
		}
        },
	
	components:{
	modelName
	}
        }
</script>
<style scoped>
.Addarticle{
 width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;
}
.addNamePar{
  padding:0 24px;
}
.addName{
 overflow:hidden;
 font-size:14px;
 margin-top:20px;
}
.addName span{
margin-right:14px;

}


.categorName{
 position:relative;
margin-top:20px;
overflow:hidden;
}
.categorName span{
display:block;
  font-size:14px;
  line-height:32px;
  float:left;
  margin-right:14px;
}
.location{
  position:absolute;
  bottom:-7px;
  left:254px;
  color:#c7c8c9;
  font-size:14px;
}
</style>


