<template>
    <div class="Carousel">
   	<div class="name">多图展示</div>
     <div class="swiper-container_img">

  <div class="swiper-wrapper swiper_scroll">
 
      <div class="swiper-slide left" v-for="item in carousel"><a href="#"><img v-bind:src="item">
         
      </a></div>
    
	   
	   </div>
  </div>
 <div class="pagination"></div>

</div>
</div>

       </div>
    
</template>

<script>
import $ from "jquery"
import swipercss from "../../../assets/css/idangerous.swiper.css"
import Swiper from "../../../../static/idangerous.swiper.min.js"
export default {
      data(){
        return{
	  
	}
      },
      
 mounted(){
      	this.swperis();
      },
      
      methods:{
      	swperis(){
    
    var mySwiper = new Swiper('.swiper-container_img',{
   slidesPerView :2,
	  autoplayDisableOnInteraction : false,
      autoplay: 5000,
     paginationClickable: true
  }); 
      	}
},
     computed:{
     	carousel(){
     		 return this.$store.state.moduleElement.photos;
     	}
     }
}
</script>

<style scoped>
	.name{
		color:#087800;
		 padding:4px;
  padding-left:2%;
  font-size:14px;
  background-color:#fff

	}
.Carousel{
	width:100%;
	
	position: relative;
	overflow:hidden;
}

.swiper-container_img{
	width:100%;
	 height:60px;
}
.swiper-wrapper{
	width:100%;
	 height:100%;
	overflow: hidden;
}
.swiper-slide {
   width:100%;
   height:100%;
	color: #fff;
	font-size: 36px;
	text-align: center;
}
.swiper-container_img a {
	width:100%;
	 height:100%;
	display: block;
	padding:0 4%;
}
.swiper-container_img a img{
	width:100%;
	height:100%;
	border-radius:4%;
}

</style>



