<template>
    <div class="bgImg">
      <img :src=bgImg>
   </div>
</template>

<script>
export default {
  data(){
     return{
      
     }
  },

   computed:{
   	
     bgImg(){
   
       return this.$store.state.CateGory.bgImg
      },
  },
}
</script>

<style scoped>
.bgImg{
  width:100%;
  position:absolute;
  height:100%;
  top:0px;
  left:0px;
  z-index:-1;
}
.bgImg img{
  width:100%;
  height:100%;
}
</style>



