<template>
  <div class="categorName">
	    <span>标题 :</span>
              <Input v-model="text" placeholder="请输入..." style="width: 200px;"></Input>
	      <span class="location"><Checkbox v-model="single">隐藏</Checkbox></span>
	 </div>

</template>

<script>

export default {
   data(){
     return {
        text:"",
	single:false
     }
   },
   methods:{
     /*调用accept事件，传入val数据*/
     callback:function(val){
       this.$emit('accept',val,this.single)
     }
   },
   /*当内容text改变触发callback事件*/
   watch:{
     text:function(val,oldVal){
        
         this.callback(val)
       }
   }

}
</script>

<style scoped>
.categorName{
 position:relative;
}
.categorName span{
display:block;
  font-size:14px;
  line-height:32px;
  float:left;
  margin-right:14px;
}
.location{
  position:absolute;
  bottom:-7px;
  left:254px;
  color:#c7c8c9;
  font-size:14px;
}
</style>



