<template>

   <div class="pagings">

   </div>

</template>

<script>

import Mfooters from '../CenterSide/module/Mfooters'

import $ from "jquery"
export default {
	data(){
		return {
			
		}
	},
	created(){
	
		var DataUrl = this.GetQueryString("imgs");
		
		this.$store.dispatch("DataUrl",DataUrl)
	},
   methods:{
      GetQueryString:function(name){
      var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
     var r = window.location.search.substr(1).match(reg);
     if(r!=null)return  unescape(r[2]); return null;
     }
   }, 
  components:{
  Mfooters
  },
}
</script>

<style scoped>
body{
    font-size:62.5%;
}

.pagings{
 width:100%
}
img{
 width:100%;
}
</style>



