<template lang="html">
    <div class="CenterSide">
      <div class="phone_wrap">
        <div class="Mono-header"  id="wrapper" ref="shopBox">
           <div class="Mono" ref="Wr_H" href="#hello-world" name="hello-world">
          <router-view></router-view>
              <bgImg></bgImg>
            <component v-for="item in items" :is="item"></component>
	   </div>
        </div>
      </div>
    </div>
</template>

<script>
var b =0

//import BScroll from 'better-scroll'
import bgImg from './module/bgImg'
import menus from './module/menu'
import searchs from './module/search'
import Carousel from './module/Carousel'
import Mfooters from './module/Mfooters'
import artiList from './module/artiList'
import Product from './module/Product'
import announcement from './module/announcement'
import categories from './module/categories'

import photosModule from './module/photos.module'
export default {

 data(){
   return{
    items:[],
     H:0
   }
 },

 created(){
     this.$root.eventHub.$on("Module",(model) => {

       this.items.push(model)
     

     })
      this.$root.eventHub.$on("eventName",(target) => {
         this.submit(target)
})

},

 methods:{

   submit:function(){
         var Html= this.$refs.shopBox.innerHTML
}
 },


   components:{
   bgImg,
   menus,
   searchs,
   Carousel,
   Mfooters,
   artiList,
   Product,
   announcement,
   categories,
   photosModule

   },
}
</script>

<style scoped>

.CenterSide{
  width: 346px;
  height:605px;
  margin-left:40px;
  float:left;
}
.phone_wrap{
  width:100%;
  height:605px;
  background: url(../../assets/images/1.png) no-repeat;
  background-size: 100% 100%;
  overflow:hidden;
}

.Mono-header{
  width:292px;
  height:455px;
  margin:78px auto;
  position: relative;;
     overflow-x: hidden;
}
.Mono{
width:100%;
height:100%;
 position:relative;

  padding-bottom:60px;
}



</style>



