<template>

<div class="Category">
    <!--头部-->
 <div v-show="seed!=true" style="padding-bottom:60px">
<modelName :Textname="Textname" v-on:Ctrl="Ctrl" :_index=[false,0]></modelName>

 <div class="headerStyle">
     <span>样式 :</span>
     <div class="conent">
        <span v-on:click="fn=1" v-bind:class="{'par':fn===1}"><img src='../../assets/images/4.jpg'>
	   <Icon  v-show="fn===1" type="checkmark-round" v-bind:class="{'selecte':fn===1}"></Icon>
	</span>
	<span v-on:click="fn=2" v-bind:class="{'par':fn===2}">
	<img src='../../assets/images/3.jpg'>
	 <Icon v-show="fn===2" type="checkmark-round" v-bind:class="{'selecte':fn===2}"></Icon>
	</span>
	<span v-on:click="fn=3" v-bind:class="{'par':fn===3}"><img src='../../assets/images/2.jpg'>
	 <Icon v-show="fn===3" type="checkmark-round" v-bind:class="{'selecte':fn===3}"></Icon>
	</span>
	<span v-on:click="fn=4" v-bind:class="{'par':fn===4}"><img src='../../assets/images/1.jpg'>
	  <Icon v-show="fn===4" type="checkmark-round" v-bind:class="{'selecte':fn===4}"></Icon>
	</span>
	 </div>
 </div>

<div class="footers">
  <div class="footerlist">
    <span>链接列表 : </span>
    <span><a>导入栏目数据</a>
          <a v-on:click.stop="addOr()">添加链接</a>
    </span>
  </div>

  <div class="dataList">
     <div class="list">
  <span>标题</span>
	<span>链接地址</span>
	<span>图标</span>
	<span>背景</span>
	<span>显示</span>
	<span>操作</span>
      </div>

  <div class="data" v-for="(item,index) in dataList">
    <div>{{item.name}}</div>
    <div>{{item.url}}</div>
    <div><div style="width:20px;padding:0;height:20px;margin:0 auto;float: none;"><img v-bind:src="item.icon" style="width:100%"></div></div>
    <div><i class="background_Color" v-bind:style="{background:dataList[index].color}"></i></div>
    <div v-on:click="ico(index)"><Icon :type="item.Accor[item.Accor[2]]"></Icon></div>
    <div>

<span v-on:click="editor(index)">
		     	<Icon type="compose" style="color:#c7dae4"></Icon>

		     </span>

 <span v-on:click="Movedown(index)">
    <Icon type="arrow-up-c" v-show="index!=0" style="color:#c7dae4"></Icon>
 </span>

                     <span v-on:click="Moveup(index)">
		     <Icon type="arrow-down-c" v-show="index!=dataList.length-1" style="color:#7dc9fb"></Icon>
		     </span>

		     <span v-on:click="Movedelete(true,index)"><Icon type="close" style="color:#c7dae4"></Icon></span>

    </div>
  </div>
   </div>
       <div class="Button">
     <Button type="text" v-on:click="submit">确定</Button>
       <Button type="text" v-on:click="Ctrl">取消</Button>
  </div>
</div>
</div>

 <div class="addPoints" v-if=seed>
 <addPoints v-on:passEvent ="upData"></addPoints>
 </div>

 </div>
</template>

<script>
var exchange;
var In = 0;
import modelName from "../PublicModule/modelName"
import NameModel from "../PublicModule/NameModel"
import addPoints from "./input/addPoints"
export default {

  data(){
    return {
      Textname:"分类导航模块",
     par:"par",
      dig:"",
      fn:1,
      dataList:[]
    }
  },

  methods:{
  	//更改是否显示
    ico:function(Index){


     if(this.dataList[Index].Accor[2]===0){

     this.dataList[Index].Accor[2]=1;

     }else{
     this.dataList[Index].Accor[2]=0;
     }
     this.$set(this.dataList[Index].Accor,2,this.dataList[Index].Accor[2]) //及时刷新更改的数据

    },


      Movedown:function(Index){
         exchange = this.dataList[Index];
	 this.dataList[Index] = this.dataList[Index-1];
	 this.dataList[Index-1] = exchange
        this.$set(this.dataList,Index,this.dataList[Index])
      },
      Moveup:function(Index){

          exchange = this.dataList[Index];
	 this.dataList[Index] = this.dataList[Index+1];
	 this.dataList[Index+1] = exchange

	  this.$set(this.dataList,Index,this.dataList[Index])
      },
      Movedelete:function(nodesc,Index){
         this.dataList.splice(Index,1)
        this.$Notice.info({
                    title: '删除成功',
                    desc: nodesc ? '' : '成功删除'
                });
      },
      editor:function(Index){

       this.addOr(Index)
      },

      addOr:function(Index){
          // 注意： Index 是用来判断当前是添加新的内容，还是修改当前点击的内容（Index代表当前点击的下标）
            if(this.seed===false){
	      this.$store.dispatch("seed",[true,1,Index])
	   }else{
	      this.$store.dispatch("seed",[false,2,Index])
	   }
      },

  Ctrl:function(){

           this.$emit("on",-1)
      },

  submit:function(){
  	 this.$store.dispatch("menu",[this.dataList,this.fn])
         if(In===0){
         	//this.$store.state.moduleElement.menu = [this.dataList,this.fn]

	     this.$root.eventHub.$emit("Module","menus")
	  In++;
	}

      this.Ctrl();
      },
     upData(data){      //更新数据 从子组件addPoints传递过来
     	 if(this.seed_index>=0){
     	 	this.dataList[this.seed_index] = data
     	 }else{
     	 	this.dataList.push(data)
     	 }

     }


  },
  computed:{
         // 获取一级状态值
     seed(){
     
        return this.$store.state.CateGory.seed
      },
      seed_index(){
        return this.$store.state.CateGory.seed_index
      },
  },
   components:{
    modelName,
    NameModel,
    addPoints
   }

}
</script>

<style scoped>

.background_Color{
	width:20px;
	height:20px;
	display: block;
    margin:0 auto;
}
.Category{
  width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
 
  position:relative;


}
.headerStyle{
 padding:0 24px;
 margin-top:20px;
 font-size:14px;
}
.conent{
 width:100%;
 overflow:hidden;
 margin-top:20px;
}
.conent span {
width:25%;
 float:left;
 text-align:center;
 border:1px solid #fff;
 position:relative;

}
.conent span img {
  width:80%;
}

.footers{
 padding:0 24px;
 margin-top:20px;
 font-size:14px;
}
.footerlist{
 overflow:hidden;
}
.footerlist span:first-child{
float:left;
}
.footerlist span:last-child{
float:right;
}
.footerlist span:last-child a{
margin-left:20px;
}
.list{
  overflow:hidden;
  margin-top:20px;
}
.list span {
 float:left;
 width:16.66666666%;
 text-align:center;
}
.data{
 overflow:hidden;


}
.data div{
  float:left;
  width:16.6666666%;
  text-align:center;
  font-size:14px;
  padding:10px 0;
  word-break:keep-all;
  text-overflow:ellipsis;
  white-space:nowrap;
  height:40px;
   overflow:hidden;
}
.data div .ivu-icon-checkmark-round{
color:rgb(45,141,56)
}
.data div .ivu-icon-close-round{
 color:red;
}

.column_ico span {
  margin-right:6px;
  font-size:14px;
}

  .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}



.conent .par{
border:1px solid rgb(101,151,86)
}

 .selecte{
  width:16px;
  height:16px;
  background:rgb(101,154,86);
  color:#fff;
  line-height:16px;
  text-align:center;
  border-radius:2px;
  position:absolute;
  right:0;
  bottom:0;
}
</style>



