<template>
    <div class="menu">
       <ul class="menu_ul" v-if="menu[1]===1" >
         <li v-for="(item,index) in menu[0]">
	 <a href="#">
	   <p class="menu_Img" v-bind:style="{background:item.color}"><img v-bind:src="item.icon"></p>
	   <span v-text="item.name" v-bind:style="{fontSize:item.Size}">标题</span>
	   </a>
	 </li>
</ul>
   </div>
</template>

<script>
import $ from "jquery"
export default {
     data(){
        return{                                                                             
	   dataArray:[],
	 
	   
	}
     },
     computed:{
     	menu(){
     		
     		return this.$store.state.moduleElement.menu
     		
     	}
     },
     methods:{
      
     },
    
}
</script>

<style scoped>

.ivu-menu-horizontal .ivu-menu-item, .ivu-menu-horizontal .ivu-menu-submenu{
 padding:0 4px;
}
.menu{
  
}
.menu_ul{
  width:100%;
  overflow:hidden;

  padding-bottom:10px;
}
.menu_ul li a {
   
    color:#fff;
    overflow: hidden;
    display: block;
}
.menu_ul li {

 width:25%;

 float:left;
 text-align:center;
 color:#fff;
}
.menu_ul li a .menu_Img{
	width:70%;
	height:3rem;
	text-align:center;
	border-radius:50%;
	margin:0 auto;
}
.menu_ul li a p img{
  width:50%;
  margin-top:25%;
  text-align:center;
}
.menu_ul li a  span{
	display: block;
	padding:2%
}
</style>



