<template>
   <div class="announcement">
   	<div v-show="seed!=true">
     <modelName :Textname="Textname[0]" v-on:Ctrl="Ctrl" :_index=[false,0]></modelName>
     <div class="twoactive">
     <span v-on:click="switchsBan(true)" v-bind:class="[rowls?'active':'']">常规</span><span v-on:click="switchsBan(false)" v-bind:class="[rowls!=true?'active':'']">高级</span>
     </div>

<!--常规-->
<div class="conventional" v-show="rowls!=false">
  <NameModel style="margin-left:24px" v-on:accept="accept"></NameModel>
  <!--排版样式-->
  <div class="typography">
    <span>排版样式 : </span>
    <div class="right">
         <div class="TheIco">
	        <i class="iconfont" v-for="(item,index) in icon" style="border:1px solid #fff;" v-bind:class="item.icon" v-bind:style={color:item.color} v-on:click="icons(index)" ref="inconstyle"></i>
	 </div>

	<!--<div class="check">
	      <Radio-group v-model="animal">
        <Radio label="自定义"></Radio>
        <Radio label="无"></Radio>

    </Radio-group>
	</div>-->
	 <div class="upload" v-show="animal===animal2">
	   <div class="uploadIco">
	   <div>
	      <i class="iconfont icon-xiangji"></i>
	      </div>
	   </div>
	   <div><Upload action="//jsonplaceholder.typicode.com/posts/">
        <Button type="ghost" icon="ios-cloud-upload-outline">上传图片</Button>
    </Upload>
      <i>建议图片大小: 40*40</i>
    </div>
	 </div>
     </div>
  </div>
<div class="dataList">
<div class="dataLfet">
 <span>公告列表 :</span>
 <span v-on:click.stop="addLinks">添加公告</span>
 </div>
 <div class="datas">
   <div class="listHeader">
   <span>内容</span>
   <span>链接地址</span>
   <span>操作</span>

   </div>
   <div class="data" v-for="(item,index) in data">
     <div>{{item.name}}</div>
     <div>{{item.url}}</div>
     <div>
           <span v-on:click.stop="editor(index)"><Icon type="compose" style="color:#c7dae4"></Icon></span>
                     <span v-on:click="Movedown(index)"><Icon type="arrow-up-c" v-show="index!=0" style="color:#c7dae4" ></Icon></span>
                     <span v-on:click="Moveup(index)">
		     <Icon type="arrow-down-c" v-show="index!=data.length-1" style="color:#7dc9fb"></Icon>
		     </span>

		     <span v-on:click="Movedelete(true,index)"><Icon type="close" style="color:#c7dae4"></Icon></span>
     </div>
   </div>
 </div>
</div>


</div>
<!--高级-->
<div class="senior" v-show="rowls!=true">

  <div><span>滚动方向 :</span>
          <Select v-model="model1" style="width:100px">
        <Option v-for="item in direction" :value="item.value" :key="item">{{ item.label }}</Option>
    </Select>
   </div>
   <div><span>滚动速度 :</span>
          <Select v-model="model2" style="width:100px">
        <Option v-for="item in velocity" :value="item" :key="item">{{ item }}</Option>
    </Select>
   </div>
   <div><span>连续滚动 :</span>
        <Radio-group v-model="disabledGroup">
        <Radio label="是"></Radio>
        <Radio label="否"></Radio>

    </Radio-group>
   </div>
</div>
<!--公告通知模块-->
<div class="Button">
     <Button type="text" v-on:click.stop="submit">确定</Button>
       <Button type="text" v-on:click="Ctrl">取消</Button>
  </div>
<!--添加公告-->
<div class="addMoParen" v-if="switchs2">
   <addannouncement v-on:hideLink="hideLink" v-on:addEvent = "addEvent" v-on:amend="amend" v-bind:index = "index"></addannouncement></div>
   </div>
   </div>
</template>

<script>
var exchange;
var In = 0;
import modelName from "../PublicModule/modelName"

import addannouncement from "./input/addannouncement"
import NameModel from "../PublicModule/NameModel"
export default{
data(){
  return{
    Textname:["公告通知模块","添加公告"],
    show:true,
    rowls:true,
     animal: '',
     animal2:'自定义',
    
    index:-1,
     switchs2:false,
     icon:[{"icon":"icon-gonggao","color":"#f7d11e","index":0},
           {"icon":"icon-tongzhi2","color":"#f54d4f","index":1},
           {"icon":"icon-iconfont04","color":"#51bbe0","index":2},
           {"icon":"icon-tongzhi","color":"#388fd5","index":3},
           {"icon":"icon-tongzhi1","color":"#ff3333","index":4},
           {"icon":"icon-tongzhi1","color":"#ff9900","index":5},
           {"icon":"icon-tongzhi3","color":"#fd5642","index":6},
     ],
     setValue:{"name":"文章列表","_index":1,"icon":"icon-gonggao"}, //各个设置效果
     data:[
      {
      name:"关于我们",
      url:"http://www.baidu.com"

      },
      { name:"关于栏目",
      url:"http://www.baidu1.com"},
      { name:"关于他们",
      url:"http://www.baidu2.com"}
     ],
      disabledGroup: '是',
      velocity:[1,4,6,8,10],
        direction: [
                    {
                        value: 'up',
                        label: '向上'
                    },
                    {
                        value: 'down',
                        label: '向下'
                    },
                    {
                        value: 'left',
                        label: '向左'
                    },
                    {
                        value: 'right',
                        label: '向右'
                    }
                
                ],
                model1: '',
		model2: '',
		gather:{
			name:"公告通知",
			accept:"",
			icon:"",
			dire:"left",
			speed:"2",
			Data:"",
		}


  }
},
 methods:{
 	/*这个事件是由NameModel来跟输入的数据模块触发*/
      accept:function(data,accept){
          this.gather.name = data
	      this.gather.accept = accept
      },
 	//addannouncement传递过来的数据
 	addEvent(data){
 	 
 		this.data.push(data)
 	},
 	// 根据点击的事件。来修改当前的数据
 	amend(data,index){
 		
 		this.data[index].name = data.name
 		this.data[index].url  = data.url
 	},
 	
 	icons(index){
 		for(var i = 0 ;i < this.$refs.inconstyle.length;i++){
 			this.$refs.inconstyle[i].style.borderColor = "#fff";
 		}
 		this.gather.icon = this.icon[index];
 		
 		this.$refs.inconstyle[index].style.borderColor=this.icon[index].color
 	
 	this.setValue.icon = this.icon[index].icon
 	},
 	
    switchs:function(fn){
	     this.show = fn;
	   },
            switchsBan:function(fn){
	      this.rowls = fn
	    },
	 Movedown:function(Index){
         exchange = this.data[Index];
	 this.data[Index] = this.data[Index-1];
	 this.data[Index-1] = exchange
         this.$set(this.data,Index,this.data[Index])

      },

      Moveup:function(Index){
          exchange = this.data[Index];
	 this.data[Index] = this.data[Index+1];
	 this.data[Index+1] = exchange
	  this.$set(this.data,Index,this.data[Index])
      },
      Movedelete:function(nodesc,Index){
         this.data.splice(Index,1)
        this.$Notice.info({
                    title: '删除成功',
                    desc: nodesc ? '' : '成功删除'
                });
      },
      editor:function(Index){
             if(Index>=0){
             	this.addLinks();
             	this.index = Index
             }
      },
    Ctrl:function(){

           this.$emit("on",-1)
      },
      
      //提交数据
       submit:function(){
       	this.gather.Data = this.data;
       
        this.$store.dispatch("announcement",this.gather)	
       	// this.$store.dispatch("announcement")
       	  if(In===0){
       	  this.$root.eventHub.$emit("Module","announcement")
       	   In++;
       	  }
        this.Ctrl();
      },
      addLinks:function(){

          this.switchs2 =true

       },
       hideLink:function(no){
          
            this.switchs2 = no;
        }
    },
    watch:{
    	 model1:function(){
    		this.gather.dire = this.model1;
    	 },
    	 model2:function(){
    	 	this.gather.speed = this.model2;
    	 }
    	
    },
    computed:{
         // 获取一级状态值
     seed(){
     
        return this.$store.state.CateGory.seed
      },
      seed_index(){
        return this.$store.state.CateGory.seed_index
      },
  },
 components:{
    modelName,
    NameModel,

    addannouncement

   }
}
</script>

<style scoped>
.annMoParen{
width:100%;
position:absolute;
 left:0px;
 top:0px;

 padding-bottom:60px;
 background:#fff;
 z-index:9999;
 background:rgba(0,0,0,0.3);
 height:100%;
}
.addMoParen{
width:300px;
position:absolute;
 right:-306px;
 top:0px;

 padding-bottom:60px;
 background:#fff;
 z-index:9999;
}
.announcement{
   width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;

}

.twoactive{
  padding:0 24px;
  background:#e6e6e6;
  font-size:12px;
}
.twoactive > span{
  height:100%;
  padding:0 8px;
  display:inline-block;
}
.twoactive .active{
  background:#fff;
  color:#565656;
}
.senior,
.conventional{
 font-size:14px;
 padding:0 24px;
 margin-top:20px;
}
.typography {
 overflow:hidden;
 margin-top:20px;
}
.typography > span{
 float:left;
 padding:10px 0;

}
.typography .right{
 float:left;
}
.TheIco {
 overflow:hidden
}
.TheIco i{
 border:1px solid rgba(0,0,0,0);
 font-size:28px;
 display:block;
 float:left;
  margin-left:14px;
}
.upload,
.check{
 margin-top:20px;

}
.upload{
 overflow:hidden
}
.upload > div{
 float:left;
}
.uploadIco{
  border:1px solid #646168;
  padding:2px;
  border-radius:4px;
  width:50px;
  text-align:center;
}
.uploadIco div{
border-radius:4px;
background:#a2d7d4;
}
.uploadIco div i{
 color:#239b92;
 font-size:24px;
}
.upload > div:last-child{
 margin-top:5px;
 margin-left:20px;
}
.upload > div:last-child i{
 color:#dcdcdc;
 font-size:12px;
}

.dataLfet{
overflow:hidden;
}
.dataLfet span:first-child{
 float:left;
}
.dataLfet span:last-child{
 float:right;
 color:#7dc9fb;
}
.data,
.listHeader {
overflow:hidden;

}
.listHeader span{
 float:left;
 width:33.33333333%;
 text-align:center;
 background:#b2c8dd;
 padding:6px 0;
}
.data > div{
width:33.3333333%;
float:left;
text-align:center;
padding:6px 0;
}

 .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}


.senior > div:nth-child(2),
.senior > div:nth-child(3){
margin-top:20px;
}
.senior > div > span{
margin-right:14px;
}

</style>



