<template>

 <div class="Product">
    <modelName :Textname="Textname[0]" v-on:Ctrl="Ctrl" :_index=[false,0]></modelName>
     <div class="twoactive">
     <span v-on:click="switchsBan(true)" v-bind:class="[rowls?'active':'']">常规</span><span v-on:click="switchsBan(false)" v-bind:class="[rowls!=true?'active':'']">高级</span></div>
   <div class="conventional" v-show="rowls!=false">
      <NameModel v-on:accept="accept"></NameModel>
      <!--样式打钩-->
      <div class="style">
        <span>样式 : </span>
	<div class="foucrs">

	   <div v-on:click="fn=1" v-bind:class="{'par':fn===1}"><img src="../../assets/images/8.png">
	     <Icon v-show="fn===1" v-bind:class="{'selecte':fn===1}" type="checkmark-round"></Icon>
	   </div>

	   <div v-on:click="fn=2" v-bind:class="{'par':fn===2}"><img src="../../assets/images/6.png">
	   <Icon v-show="fn===2" v-bind:class="{'selecte':fn===2}" type="checkmark-round" ></Icon>
	   </div>

	    <div v-on:click="fn=3" v-bind:class="{'par':fn===3}"><img src="../../assets/images/7.png">
	     <Icon type="checkmark-round" v-show="fn===3" v-bind:class="{'selecte':fn===3}"></Icon>
	   </div>
	</div>
      </div>

    <!--选择文章-->
    <div class="spanText">
      <span>选择文章 : </span>
	   <div class="categorflst">

    <div class="labels">
	     <Radio-group v-model="animal">
                <Radio label="选择指定文章"></Radio>
                <Radio label="选择文章分类"></Radio>

              </Radio-group>
	       </div>
	       </div>
    </div>

  <!--搜索-->

    <div class="large" v-show="animal===animal1">
      <AutoComplete
        v-model="value3"
        :data="list"
        @on-select="onSelect"
        :filter-method="filterMethod"
        placeholder="input here"
        style="width:200px">
    </AutoComplete>

      <div class="add">
        <div class="addLeft" >

	  <div id="staggered-list-demo">
      <ul>
      	 <li v-for="item in list" v-on:click="replace(item)">{{item}}</li>
      	
      </ul>
        
</div>
 <div class="addcanp" @click="modal1 = true"><Icon type="plus-round"></Icon>添加产品</div>


 <!--添加按钮弹出层-->
 <Modal
        v-model="modal1"
        title="添加产品"
        @on-ok="ok"
       
        @on-cancel="cancel">
        <Input v-model="value" placeholder="请输入..." style="margin:20px 14px;width: 200px"></Input>
    </Modal>

	</div>
	
	
	 <div class="addcenter">
	   <Icon type="play"></Icon>
	 </div>
	 <div class="addright">
	   <div v-for="(item,index) in rightdata" class="hdie">
	     <div class="column_list">{{item}}</div>

		 <div class="column_list column_ico">
		    <span v-on:click="Movedown(index)"><Icon type="arrow-up-c"   v-show="index!=0" style="color:#c7dae4"></Icon></span>
                     <span v-on:click="Moveup(index)">
		     <Icon type="arrow-down-c" v-show="index!=rightdata.length-1" style="color:#7dc9fb"></Icon>
		     </span>

		     <span v-on:click="Movedelete(true,index)"><Icon type="close" style="color:#c7dae4"></Icon></span>
		 </div>


	   </div>
	 </div>
      </div>
    </div>
<!--文章分类-->
<div class="listLable" v-show="animal===animal2">
   <Checkbox-group v-model="fruit">
      <Checkbox :label=item.bigclassname v-for="item in article"></Checkbox>
      
  </Checkbox-group>
    <div class="catText">产品展示将最多显示10个产品，其余的产品将在产品列表页中显示</div>
</div>


   </div>

   <!--编辑图片-->
  <div class="senior" v-show="rowls!=true">
       <div class="basis">
          <h2>基础设置</h2>
	   <div class="basischild">
	   <span style="float:left;margin-right:14px;margin-left:28px;">显示产品 :</span>
	   <input type="text" class="batext"><i>个</i>
	   </div>

           <div class="seniorleft">
	   <span class="left" style="margin-left:28px;">显示更多 : </span>
	   <div class="right">
	   <div>
	      <Radio-group v-model="animals">
               <Radio label="是"></Radio>
                <Radio label="否" style="margin-left:20px;"></Radio>
                </Radio-group>
		</div>
             <div class="right_Mar">
	       <Input v-model="value" placeholder="填入跳转的页面" style="width: 300px"></Input>
	        <Button type="ghost">设置连接</Button>
	     </div>
		</div>
	   </div>
	   <!--文字设置-->
        <div class="seniorleft">
	   <h2>文字设置 :</h2>
	   <div class="basischild">
	    <span class="left">显示产品名称 : </span>
	    <Radio-group v-model="text">
               <Radio label="是"></Radio>
                <Radio label="否" style="margin-left:20px;"></Radio>
                </Radio-group>
	   </div>
	</div>

<!--跳转设置-->
<div class="seniorleft">
 <h2>跳转设置 :</h2>
  <div class="basischild">
	   <span class="left">点击图片跳转 : </span>
	   <div class="right">
	   <div>
	      <Radio-group v-model="rul">
               <Radio label="产品链接页面"></Radio>
                <Radio label="自定义" style="margin-left:20px;"></Radio>
                </Radio-group>
		</div>
             <div class="right_Mar">
	       <Input v-model="value" placeholder="填入跳转的页面" style="width: 300px"></Input>
	        <Button type="ghost">设置连接</Button>
	     </div>
		</div>
	   </div>
</div>
       </div>
  </div>
   <div class="Button">
      <Button type="text" v-on:click.stop="submit">确定</Button>
        <Button type="text" v-on:click="Ctrl">取消</Button>
  </div>
 </div>

<!--编辑图片-->


</template>

<script>
var exchange;
var In=0
import modelName from "../PublicModule/modelName"
import NameModel from "../PublicModule/NameModel"
export default {
   data(){
     return {
        show:true,
        rowls:true,
          value3: '',
              
        Textname:["文章列表模块","编辑图片"],
	animals:"",
	fruit:[],
	text:"",
	rul:"",
	value:"",
	fn:1,
	value4:"",
	getData:[],
    setValue:{"name":"文章列表","_index":1}, //各个设置效果
	animal:"选择指定文章",
	animal1:"选择指定文章",
	animal2:"选择文章分类",
	 query: '',
    list: [
       'Bruce Lee',
      'Jackie Chan' ,
      'Chuck Norris' ,
       'Jet Li' ,
       'Kung Fury' 
    ],
    modal1: false,
	rightdata:[]
     }
   },
  created(){
   	  this.$store.dispatch("article")   // 页面加载触发文章列表事件获取数据
   },
    computed: {
      
      // 获取一级状态值
     seed(){
        return this.$store.state.CateGory.seed
      },
      seed_index(){
        return this.$store.state.CateGory.seed_index
      },
    article:function(){
    return this.$store.state.CateGory.article  //获取文章列表分类数据
   
    }
  },
  
   methods:{
   	//获取的标题的
   	accept(name){
   		
   		this.setValue.name = name
   	},
   	//查找比对
    filterMethod (value, option) {
    	  
                return option.toUpperCase().indexOf(value.toUpperCase()) !== -1;
           },
           //查找的值赋值给右边的数组
      onSelect(value){
         this.replace(value)
      },
    ok () {
             
        this.list.push({msg:this.value})
        this.$Message.info('添加成功')
		this.value="";
            },
            cancel () {
                this.$Message.info('点击了取消');
   },
  
	   switchs:function(fn){
	     this.show = fn;
	   },
            switchsBan:function(fn){
	      this.rowls = fn
	    },


      Movedown:function(Index){

         exchange = this.rightdata[Index];
	 this.rightdata[Index] = this.rightdata[Index-1];
	 this.rightdata[Index-1] = exchange
this.$set(this.rightdata,Index,this.rightdata[Index])
      },
      
      Moveup:function(Index){
          exchange = this.rightdata[Index];
	 this.rightdata[Index] = this.rightdata[Index+1]
	 this.rightdata[Index+1] = exchange
   this.$set(this.rightdata,Index,this.rightdata[Index])
      },
      
      Movedelete:function(nodesc,Index){
         this.rightdata.splice(Index,1)
        this.$Notice.info({
                    title: '删除成功',
                    desc: nodesc ? '' : '成功删除'
                });
      },
    Ctrl:function(){

           this.$emit("on",-1)
      },
          //把左边的值传递给右边
      replace:function(Index){

   this.rightdata.push(Index)

      },
      // 把 数据传给store在处理渲染到 界面
       submit:function(){
        if(In===0){
	  this.$root.eventHub.$emit("Module","artiList")
	  In++;
	}
    
         
      if(this.animal == this.animal1){
      	
      	this.getData.push(this.rightdata)
      	this.getData.push(this.setValue)
        this.$store.dispatch("articeList",this.getData)
     }else{
     	
     	this.getData.push(this.fruit)
      	this.getData.push(this.setValue)
         this.$store.dispatch("articeList",this.getData)
      }
        this.Ctrl();
      },
    

	},
	watch:{
		fn:function(){
			this.setValue._index = this.fn
		}
	},
   components:{
    modelName,
    NameModel
   }

}
</script>

<style scoped>
i{
  font-style:normal;
}
.pictures,
.Product{
  width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;


}
.twoactive{
  padding:0 24px;
  background:#e6e6e6;
  font-size:12px;
}
.twoactive > span{
  height:100%;
  padding:0 8px;
  display:inline-block;
}
.twoactive .active{
  background:#fff;
  color:#565656;
}

.conventional{
 padding:0 24px;
 margin-top:20px;
}

.style{
 font-size:14px;
 overflow:hidden;
 margin-top:20px;
}
.style span{
 float:left;
 line-height:40px;
}
.foucrs{
  float:left;
  overflow:hidden;
}

.foucrs div {
 float:left;
 margin-left:14px;
 margin-right:14px;
 padding:10px;
 border:1px solid #fff;
 position:relative
}
.foucrs .par{
   border:1px solid rgb(101,154,86);
}
.foucrs div .selecte{
  width:16px;
  height:16px;
  background:rgb(101,154,86);
  color:#fff;
  line-height:16px;
  text-align:center;
  border-radius:2px;
  position:absolute;
  right:0;

  bottom:0;
}
.spanText{
 margin-top:20px;
 overflow:hidden;
}
.spanText span {

 font-size:14px;
 float:left;
}
.right_Mar,
.large{
 margin-top:20px;
}
.add{
 width:100%;
 overflow:hidden;
 margin-top:20px;
}
.addLeft{
width:45%;
float:left;
border:1px solid rgb(126,201,255);
height:200px;
font-size:14px;
padding:10px;
position:relative;
padding-bottom:45px;
}
.addcenter{
 width:10%;
 float:left;
 height:200px;
color:rgb(171,191,218);
font-size:28px;
text-align:center;
line-height:200px;
}
.addright{
 width:45%;
 float:left;
 height:200px;
border:1px solid rgb(126,201,255);
font-size:14px;
padding:10px

}
.hdie{
 overflow:hidden
}
.column_list{
 float:left;
}
.column_ico{
 float:right;
}
.addcanp{
 text-align:center;
 border:1px dashed rgb(207,209,208);
 padding:4px 0;
 position:absolute;
 bottom:10px;
 width:80%;
 left:10%;
}
.listLable{

}
.ivu-checkbox-group-item{
  width:12%;
  margin:0px;
  margin-top:20px;

  text-align:center;

}
.catText{
    margin-top:10px;
    background:#ffb399;
    color:#fff;
    display:inline-block;
    padding:2px 10px;
    font-size:14px;
}
.Button{
 position:absolute;
 right:20px;
 bottom:10px;
}

.senior{
 padding:0 24px;
margin-top:20px;
  font-size:14px;
 }
 .senior h2{
   font-size:16px;
 }
 .Hpad{
 padding:0;
 height:0
 }
 .seniorleft{
  overflow:hidden;
  margin-top:20px;
 }
 .seniorleft .left{
  float:left;
  margin-right:14px;
 }
 .seniorleft .right{
  float:left;
 }

 .batext{
   display: inline-block;
    width: 30px;;
    height: 24px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 12px;
    border: 1px solid #dddee1;
    border-radius: 4px;
    color: #495060;
    background-color: #fff;
    background-image: none;
    position: relative;
    cursor: text;
    margin-right:14px;
    transition: border .2s ease-in-out,background .2s ease-in-out,box-shadow .2s ease-in-out;
 }
 .basischild{
  margin-top:20px;
  overflow:hidden;
 }

.Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
</style>



