<template lang="html">
 <div class="BtnNav">
    <div v-show="seed!=true">
      <modelName :Textname="Textname" v-on:Ctrl="Ctrl" :_index=[false,0]></modelName>
   <div class="btnWrap">
   <!--模板样式-->
      <div class="styleWrp">
      <div class="styleText clearfix">
       <span>模板样式 :</span>
      <styleColor v-on:Module="btnColor"></styleColor>

    </div>
     <div class="styleico">

        <div class="ico1"  v-on:click.stop="Hmltc(1)" v-bind:style="{color:classify.colors,borderColor:classify.colors}">
	   <span>
	   <Icon type="ios-home-outline"></Icon></i>
	   <p>首页</p>
	   </span>
	   <span><Icon type="ios-paper"></Icon></i>
	      <p>订单</p>
	   </span>
	   <span><Icon type="ios-cart"></Icon></i>
	     <p>购物车</p>
	   </span>
	   <span><Icon type="person"></Icon></i>
	     <p>个人中心</p>
	   </span>
	    <div v-if="classify._index!=2" style="width:20px;height:20px;background:rgb(100,158,124);color:#fff;position:absolute;right:0px;text-align:center;bottom:0px;">
	    <Icon type="checkmark-round"></Icon>
	    </div>
	</div>

	<div class="ico2" v-on:click.stop="Hmltc(2)"  v-bind:style="{background:classify.colors,borderColor:classify.colors}">
	<span> <Icon type="ios-home-outline"></Icon></i>
	  <p>首页</p>
	</span>
	   <span><Icon type="ios-cart"></Icon></i>
	    <p>购物车</p>
	   </span>
	   <span><Icon type="person"></Icon></i>
	    <p>个人中心</p>
	   </span>
                 <div v-if="classify._index!=1" style="width:20px;height:20px;background:rgb(100,158,124);color:#fff;position:absolute;right:0px;text-align:center;bottom:0px;">
	    <Icon type="checkmark-round"></Icon>
	    </div>

	</div>
     </div>

      </div>
  <!--连接列表-->
    <div class="listRul">
         <span>链接列表 : </span>
	 <div class="listname">
	     <div class="name">
	       <span>标题</span><span>链接地址</span><span>图标</span><span>显示</span><span>操作</span>
	     </div>
             <div class="feachName" v-for="(item,index) in listName">
 <div>{{item.name}}</div>
 <div>{{item.url}}</div>
 <div>{{item.img}}</div>
 <div style="color:rgb(46,141,13)"><Icon type="checkmark-round"></Icon></div>
   <div><span v-on:click="editor(index)"><Icon type="compose" style="color:#c7dae4"></Icon></span>
                     <span v-on:click="Movedown(index)"><Icon type="arrow-up-c" v-show="index!=0" style="color:#c7dae4"></Icon></span>
                     <span v-on:click="Moveup(index)">
		     <Icon type="arrow-down-c"  style="color:#7dc9fb" v-show="index!=listName.length-1"></Icon>
		     </span>
		     <span v-on:click="Movedelete(true,index)"><Icon type="close"  style="color:#c7dae4"></Icon></span>
		     </div>
	     </div>

	 </div>
    </div>
 <div class="Button">
     <Button type="text" v-on:click="submit()">确定</Button>
       <Button type="text" v-on:click="Ctrl">取消</Button>
  </div>
  </div>
  </div>
<div class="addPoints" v-if=seed>
 <addPoints v-on:passEvent ="upData"></addPoints>
 </div>

    </div>
</template>

<script>
var exchange;
var In = 0;
import styleColor from '../PublicModule/styleColor'
import modelName from "../PublicModule/modelName"
import addPoints from "./input/addPoints"
 export default {
 data(){
     return {
         Textname:"底部导航模块",

	 classify:{_index:1,colors:""},
	 listName:[
	 {name:"首页",

	  url:"[栏目] 首页",
	   img:"默认"

	 },
	  {name:"我要分销",
	  url:"[栏目] 我要分销",
	   img:"默认"

	 },
	  {name:"购物车",
	  url:"[栏目] 购物车",
	   img:"默认"

	 },
	  {name:"个人中心",
	  url:"[栏目] 个人中心",
	   img:"默认"

	 }

	 ],

     }

   },

   methods:{
    btnColor:function(tar){

          this.classify.colors = tar
       },
     editor:function(Index){
    
       this.addOr(Index)
      },
     addOr:function(Index){
          // 注意： Index 是用来判断当前是添加新的内容，还是修改当前点击的内容（Index代表当前点击的下标）
            if(this.seed===false){
	      this.$store.dispatch("seed",[true,1,Index])
	   }else{
	      this.$store.dispatch("seed",[false,2,Index])
	   }
      },
      
  upData(data){      //更新数据 从子组件addPoints传递过来
     	 if(this.seed_index>=0){
     	
     	 	this.listName[this.seed_index] = data
     	 }else{
     	 	 
     	 	this.listName.push(data)
     	 }

     },

   Hmltc:function(Index){

        this.classify._index = Index;
   },
   Movedown:function(Index){

	 exchange = this.listName[Index]
	    this.listName[Index] = this.listName[Index-1]
            this.listName[Index-1] = exchange
	this.$set(this.listName,Index,this.listName[Index])


      },
      Moveup:function(Index){
          exchange = this.listName[Index]
	      this.listName[Index] = this.listName[Index+1]
          this.listName[Index+1] = exchange
	      this.$set(this.listName,Index,this.listName[Index])
      },
   Movedelete:function(nodesc,Index){
       this.listName.splice(Index,1)
        this.$Notice.info({
                    title: '删除成功',
                    desc: nodesc ? '' : '成功删除'
                })
    },

    Ctrl:function(){

           this.$emit("on",-1)
      },
       submit:function(){
       	 this.listName.push(this.classify)
         
          this.$store.dispatch("footer",this.listName)
         if(In===0){
            
	       this.$root.eventHub.$emit("Module","Mfooters")

	  In++;
	}

        this.Ctrl();
      }
   },
  computed:{
  	        // 获取一级状态值
     seed(){
        return this.$store.state.CateGory.seed
      },
        seed_index(){
        return this.$store.state.CateGory.seed_index
      },
  },
  components:{

   styleColor,
   modelName,
   addPoints
 }
 }
</script>

<style scoped>


  .BtnNav{
    width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;

  }
  .btnWrap{
   padding:0 24px;
  }
 .styleWrp{
  margin-top:20px;
  }

  .styleText{
    margin-bottom:10px;
    font-size:14px;

  }

  .styleText > span {
   float:left;
   margin-right:14px;
  }

  .styleico{
    width:100%;
    overflow:hidden;
    margin-top:20px;
  }
  .styleico .ico2,
  .styleico .ico1{
    width:40%;
    border:1px solid rgb(17,150,219);
    float:left;
    margin-left:5%;
    position:relative;
    font-size:14px;
    overflow:hidden;
    padding:2px 0;
  }
  .styleico .ico2 span,
  .styleico .ico1 span{
   float:left;
  }
  .styleico .ico1 span {
     width:25%;
     text-align:center;
  }
 .styleico .ico2 {
  background: rgb(17,150,219);
 }
  .styleico .ico2 span{
    width:33.333333%;
    text-align:center;
    color:#fff;
  }

  .listRul{
   margin-top:20px;
   font-size:14px;
  }

.listname{
  margin-top:20px;
  border:1px solid #666;
}
.name {
 background:rgb(178,200,221)
}
  .name span{
     display:inline-block;
     width:20%;
     text-align:center;
     padding:6px 0;
  }
  .feachName{
  width:100%;
   font-size:14px;
   overflow:hidden;

  }
  .feachName > div {
    width:20%;
    float:left;
    padding:6px 0;
    text-align:center;
    border-top:1px solid #666;
  }
  .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
#borders{
 border:1px solid red;
}
</style>



