<template>
    <div class="Carousel">
      
     <div class="swiper-container">

  <div class="swiper-wrapper swiper_scroll">
 
      <div class="swiper-slide" v-for="item in carousel"><a href="#"><img v-bind:src="item">
         
      </a></div>
    
	   
	   </div>
  </div>
 <div class="pagination"></div>

</div>
</div>

       </div>
    
</template>

<script>
import $ from "jquery"
import swipercss from "../../../assets/css/idangerous.swiper.css"
import Swiper from "../../../../static/idangerous.swiper.min.js"
export default {
      data(){
        return{
	  
	}
      },
      
 mounted(){
      	this.swperis();
      },
      
      methods:{
      	swperis(){
    
    var mySwiper = new Swiper('.swiper-container',{
      loop:true,
	  autoplayDisableOnInteraction : false,
      autoplay: 5000,
     paginationClickable: true
  }); 
      	}
},
     computed:{
     	carousel(){
     		 return this.$store.state.moduleElement.carousel;
     	}
     }
}
</script>

<style scoped>
.Carousel{
	width:100%;
	height:200px;
	position: relative;
	overflow:hidden;
}

.swiper-container{
	width:100%;
	height:200px;
}
.swiper-wrapper{
	width:100%;
	height:200px;
	overflow: hidden;
}
.swiper-slide {
   width:100%;
   height:100%;
	color: #fff;
	font-size: 36px;
	text-align: center;
}
.swiper-container a {
	width:100%;
	height:200px;
	display: block;
}
.swiper-container a img{
	width:100%;
	height:100%;
}
</style>



