<template lang="html">
      <div class="column">
         <div class="column_test">
	 <span>栏目名称</span><span></span><span>操作</span><span>跳转</span>
	  </div>
      
      <div class="column_from">
           <div class="column_selemnt" v-bind:class="[item.text ? 'active' :'']" v-for="(item,index) in Arrays" v-show="item.MoDEles">
	   <div class="column_list" v-on:click="childs"> 
	   	<router-link to="/bgImg">{{item.name}}</router-link></div>
		 <div class="column_list" v-on:click="hidden(index)">{{item.disp}}</div>
		 <div class="column_list column_ico">
		
		     <span v-on:click="editor(index)"><Icon type="compose" style="color:#c7dae4"></Icon></span>
                     <span v-on:click="Movedown(index)"><Icon type="arrow-up-c" v-show="index!=0" style="color:#c7dae4"></Icon></span>
                     <span v-on:click="Moveup(index)">
		     <Icon type="arrow-down-c" v-show="index!=Arrays.length-1" style="color:#7dc9fb"></Icon>
		     </span>

		     <span v-on:click="Movedelete(true,index)"><Icon type="close" style="color:#c7dae4"></Icon></span>
		 </div>
		 <div class="column_list">
		   <input type="checkbox">
		 </div>
	   </div>





      </div>

      </div>
</template>

<script>
var exchange;
 export default {
   data(){
     return {
       Arrays:[
          { name:"首页",
	    disp:"隐藏",
	    text:false,
	    MoDEles:true
	  },
	  {
	  name:"公司简介",
	  disp:"默认",
	  text:false,
	  MoDEles:true
	  },
	  {
	  name:"新闻动态",
	    disp:"默认",
	    text:false,
	  MoDEles:true
	  },
          {
	  name:"联系我们",
	    disp:"默认",
	    text:false,
	  MoDEles:true
	  },
	  {
	  name:"产品展示",
	    disp:"默认",
	    text:false,
	  MoDEles:true
	  },
	   {
	  name:"店铺活动",
	    disp:"默认",
	    text:false,
	  MoDEles:true
	  }


       ],
     }
   },
   methods:{
      childs:function(Index){
        //console.log(this.$refs.columnList.children[0])
      },
      Movedown:function(Index){
         exchange = this.Arrays[Index];
	 this.Arrays[Index] = this.Arrays[Index-1];
	 this.Arrays[Index-1] = exchange
        this.$set(this.Arrays,Index,this.Arrays[Index])
      },
      Moveup:function(Index){
          exchange = this.Arrays[Index];
	 this.Arrays[Index] = this.Arrays[Index+1];
	 this.Arrays[Index+1] = exchange
	  this.$set(this.Arrays,Index,this.Arrays[Index])
      },
      Movedelete:function(nodesc,Index){
         this.Arrays.splice(Index,1)
        this.$Notice.info({
                    title: '删除成功',
                    desc: nodesc ? '' : '成功删除'
                });
      },
      editor:function(){
        alert("编辑")
      },
      hidden:function(Index){
           if(this.Arrays[Index].text==true){
	   this.Arrays[Index].text=false;
	    this.Arrays[Index].disp="默认";
	   }else{
	     this.Arrays[Index].text=true;
	     this.Arrays[Index].disp="隐藏";
	   }
           
      }
   }

}
</script>

<style lang="css">
.column{
height:528px;
 padding:0 33px 0 8px;
}
.column_test{
  width:100%;
  margin-top:30px;
  
  font-size:0;
  overflow:hidden;
}
.column_test span {
  width:25%;
  text-align:center;
  display:block;
 float:left;
 height:34px;
  padding:8px 0px;
  color:#5a626a;
  background:#b2c8dd;
  font-size:14px;
}

.column_selemnt{
  width:100%;
  overflow:hidden;
}
.column_selemnt > div{
width:25%;
 float:left;
 text-align:center;
 padding-top:16px;
}

.column_ico span {
  margin-right:6px;
  font-size:14px;
}
.active{
color:#c7dae4
}
</style>



