<template>
   <div class="Addproject">
   
     <modelName v-bind:Textname="Textname" v-on:Ctrl="Ctrl" :_index=[false,1]>></modelName>
     <div class="table_span" ref="table">
        <span v-bind:class="{active:Num===1}" v-on:click="switchs(1)">上传图片</span>
	<span v-bind:class="{active:Num===2}" v-on:click="switchs(2)">系统图库</span>
	<span v-bind:class="{active:Num===3}" v-on:click="switchs(3)">我的图片</span>
     </div>
  <div class="Addwrap">
  <div class="templates">
    <Toupload ref="childModle" v-show="Num===1" v-bind:arrays="uploadList" v-on:addgallery="addgallery" v-on:DeleteImg="DeleteImg"></Toupload>
    <gallery v-show="Num===2" :SystemImg="SystemImg" v-on:addImg="addImg"></gallery>
    <Igallery v-show="Num===3" :UserImage ="UserImage" v-on:addImg="addImg"></Igallery>
    </div>
    <!--待添加的图片-->
    <div class="galleryL">
      <span>待添加的图片({{uploadList.length}}/10)</span>
      <div class="ArrImg">
        <p v-for="(item,index) in uploadList" v-on:click.stop="DeleteImg(index)"><img v-bind:src="item"></p>
      </div>
    </div>


</div>
<Button @click="success(false)" style="display:none;"></Button>
  <div class="Button">
     <Button type="text" v-on:click.stop="submit">确定</Button>
       <Button type="text" v-on:click="Ctrl">取消</Button>
  </div>
   </div>
</template>
<script>
import modelName from "../../PublicModule/modelName"
import NameModel from "../../PublicModule/NameModel"
import Toupload from "./Toupload"
import gallery from "./gallery"
import Igallery from "./Igallery"
    export default {
        data(){
	  return{
	    Textname:"添加图片",
	    Num:1,
	    url:"'http://image.sonhoo.com/",
	    uploadList:[]
	  }
	},
	
	  created(){
   	  this.$store.dispatch("SystemImg")   // 执行连接系统默认图片
   	  this.$store.dispatch("UserImage")   // 执行连接用户图片
   },

    computed: {
    SystemImg:function(){
    
    return this.$store.state.CateGory.SystemImg  //获取系统默认图片
   
    },
    UserImage:function(){
    
    return this.$store.state.CateGory.UserImage  //获取用户图片
   
    }
   
  },
	methods:{
		addImg:function(url){
		if(this.uploadList.length===10){
			this.success()
		}else{
			
			this.uploadList.push(url)
		}
		
	},
	//提示信息
	     success (nodesc) {
                this.$Notice.success({
                    title: '警告',
                    desc: nodesc ? '' : '你已经超出添加的数量'
                });
            },
      //显示哪个用户的图片      
	  switchs:function(Index){
	    this.Num = Index
	   },
	   //关闭当前窗口
	   Ctrl:function(){
	    this.$emit("addImg",this.uploadList)
	     this.$emit("hideLink",false)
	   },
	   //提交数据
	   submit:function(){
	  
              this.Ctrl();
      },
      addgallery:function(arr){
       this.uploadList = arr;
       
       },
       
       DeleteImg:function(Index){
          this.uploadList[Index] = false
          this.uploadList.splice(Index,1)
	  
	 
       }
	},
	components:{
	modelName,
	NameModel,
	Toupload,
	gallery,
	Igallery
	 }
    }
</script>

<style scoped>
.Addproject{
width:100%;
  position:absolute;
  top:0px;
  z-index:99999;
  left:0px;
  background:#fff;
  padding-bottom:60px;
}
.table_span{
  
}


.table_span{
  padding:0 24px;
  background:#e6e6e6;
  font-size:12px;
}
.table_span > span{
  height:100%;
  padding:0 8px;
  display:inline-block;
}
.table_span .active{
  background:#fff;
  color:#565656;
}

.Addwrap{
 margin-top:20px;
 padding:0 24px;
 overflow:hidden;
 
 position:relative;
}
.templates{
 width:76%;
 float:left;
}
.galleryL{
 float:right;
 width:140px;
 text-align:center;
}
.ArrImg{
width:100%;
 height:400px;
 margin-top:10px;
 border:1px solid rgb(188,188,188);
}
.ArrImg p{
  width:100%;
  height:60px;
  text-align:center;
  margin-top:10px;
}
.ArrImg p img{
  width:80%;
  height:100%
}

 .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
</style>


