<template>
    <div class="figure">
   <div v-show="seed!=true">
        <modelName :Textname="Textname" v-on:Ctrl="Ctrl" :_index=[false,0]></modelName>
	 <div class="twoactive">
     <span v-on:click="switchsBan(true)" v-bind:class="[rowls?'active':'']">常规</span><span v-on:click="switchsBan(false)" v-bind:class="[rowls!=true?'active':'']">高级</span>
     </div>

<!--常规-->
<div class="basis" v-show="rowls!=false">
       <NameModel></NameModel>
        <!--样式打钩-->
      <div class="style">
        <span>样式 : </span>
	<div class="foucrs">
	   <div><img src="../../assets/images/9.png">
	     <Icon type="checkmark-round" class="selecte"></Icon>
	   </div>
	   
	</div>
      </div>
     <!--上传图片-->
   <div class="upload">
      <span>图片 : </span>
      <div class="upladelfet">
        <div class="ImgArr">
	   <ul>
	     <li v-for="(item,index) in addImg">
	    
	      <img v-bind:src="item">
	     </li>
	     <div class="Img" v-on:click.stop="addLinks">
	  <img src="../../assets/images/2.png">
	  <span>添加图片</span>
	</div>
	   </ul>
	</div>

        
      </div>
   </div>
  <div v-show="gal!=false">
   <Addproject v-on:hideLink="hideLink" v-on:addImg="addData"></Addproject>
   </div>
</div>
<!--高级-->
<div class="senior" v-show="rowls!=true">
         <div class="basis">
          <h2>基础设置</h2>
	  
           <div class="seniorleft">
	   <span class="left" style="margin-left:28px;">显示更多 : </span>
	   <div class="right">
	   <div>
	      <Radio-group v-model="animals">
               <Radio label="是"></Radio>
                <Radio label="否" style="margin-left:20px;"></Radio>
                </Radio-group>
		</div>
             
		</div>

	   </div>
      <div class="right_Mar">
	     <span class="left" style="margin-left:28px;">每页图片 : </span>
	       <Input v-model="value" placeholder="填入跳转的页面" style="width: 300px;margin-left:14px;"></Input>
	        <Button type="ghost">设置连接</Button>
	     </div>
   <!--滚动-->
    <div class="seniorleft">
	   <span class="left" style="margin-left:28px;text-indent:28px;">滚动 : </span>
	   <div class="right">
	   <div>
	      <Radio-group v-model="fig">
               <Radio label="是"></Radio>
                <Radio label="否" style="margin-left:20px;"></Radio>
                </Radio-group>
		</div>
             
		</div>

	   </div>
  <!--轮播图方向-->
  <div class="direction">
  <div class="directionL">
    <span>滚动方向 :</span>
   
     <Select style="width:60px">
        <Option v-for="item in cityList" :value="item.value" :key="item">{{ item.label }}</Option>
    </Select>
</div>
<div class="directionR">
<span>滚动方向 :</span>
    <Select style="width:60px">
        <Option v-for="item in cityLists" :value="item.value" :key="item">{{ item.labelR }}</Option>
    </Select>
    </div>
  
  </div>
	   <!--文字设置-->
        <div class="seniorleft">
	   <h2>文字设置 :</h2>
	   <div class="basischild">
	    <span class="left">显示图片描述 : </span>
	    <Radio-group v-model="text">
               <Radio label="是"></Radio>
                <Radio label="否" style="margin-left:20px;"></Radio>
                </Radio-group>
	   </div>
	</div>

<!--跳转设置-->
<div class="seniorleft">
 <h2>跳转设置 :</h2>
  <div class="basischild">
	   <span class="left">点击图片跳转 : </span>
	   <div class="right">
	   <div>
	      <Radio-group v-model="rul">
               <Radio label="图片链接页面"></Radio>
                <Radio label="无效果" style="margin-left:20px;"></Radio>
                </Radio-group>
		</div>
             
		</div>
	   </div>
</div>
       </div>
  </div>
   <div class="Button">
   <Button type="text" v-on:click.stop="submit">确定</Button>
       <Button type="text" v-on:click="Ctrl">取消</Button>
  </div>
 </div>
 
</div>

</div>
    </div>
  
</template>
<script>
var In=0
import modelName from "../PublicModule/modelName"
import NameModel from "../PublicModule/NameModel"
import Addproject from "./input/Addproject"
    export default {
    data(){
     return{
                show:true,
		rowls:true,
                gal:false,
		fig:"",
		animals:"",
		text:"",
		rul:"",
		addImg:"",
		value:"",
		Json:[],
       Textname:"多图展示轮播图",
        cityList: [
                    {
                        value: 'xiangzuo',
                        label: '向左'
			
                    },
                    {
                        value: 'xiangyou',
                        label: '向右'
			
                    }],
      cityLists: [
          { value:'gaoji',
	    labelR:'高级'
	   },
	   {
	     value:'putong',
	     labelR:'普通'
	   }
      ]
     }
    },
    methods:{
    addLinks:function(){
      this.gal = true
    },
    hideLink:function(){
    this.gal = false
    },
    addData(data){
    	this.addImg = data
    },
    switchs:function(fn){
	     this.show = fn;
	   },
            switchsBan:function(fn){
	      this.rowls = fn
	    },
    Ctrl:function(){

           this.$emit("on",-1)
      },
       submit:function(){
       this.$store.dispatch("photos",this.addImg)	
         if(In===0){
	  this.$root.eventHub.$emit("Module","photosModule")
	  In++;
	}
        this.Ctrl();
      }
    },
      components:{
      modelName,
      NameModel,
      Addproject
       },
        computed:{
           // 获取一级状态值
     seed(){
        return this.$store.state.CateGory.seed
      },
      seed_index(){
        return this.$store.state.CateGory.seed_index
      },
        
     }
     }
	      
</script>

<style scoped>
.figure{
 width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;
}

.twoactive{
  padding:0 24px;
  background:#e6e6e6;
  font-size:12px;
}
.twoactive > span{
  height:100%;
  padding:0 8px;
  display:inline-block;
}
.twoactive .active{
  background:#fff;
  color:#565656;
}
.senior,
.conventional{
 font-size:14px;
 padding:0 24px;
}
.basis,
.senior{
 padding:0 24px;
margin-top:20px;
  font-size:14px;
 }
 .senior h2{
   font-size:16px;
 }
 .Hpad{
 padding:0;
 height:0
 }
 .seniorleft{
  overflow:hidden;
  margin-top:20px;
 }
 .seniorleft .left{
  float:left;
  margin-right:14px;
 }
 .seniorleft .right{
  float:left;
 }

 .batext{
   display: inline-block;
    width: 30px;;
    height: 24px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 12px;
    border: 1px solid #dddee1;
    border-radius: 4px;
    color: #495060;
    background-color: #fff;
    background-image: none;
    position: relative;
    cursor: text;
    margin-right:14px;
    transition: border .2s ease-in-out,background .2s ease-in-out,box-shadow .2s ease-in-out;
 }
 .basischild{
  margin-top:20px;
  overflow:hidden;
 }

.Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
.right_Mar{
 margin-top:20px;

}

.direction{
overflow:hidden;
margin-top:20px;
margin-left:28px;
}
.directionL{
float:left;

}
.directionR span,
.directionL span {
 margin-right:14px;
}
.directionR{
 float:left;
 margin-left:14px;
}
.basis ul li {
  width:100%;
}

.style{
 font-size:14px;
 overflow:hidden;
 margin-top:20px;
}
.style span{
 float:left;
 line-height:40px;
}
.foucrs{
  float:left;
  overflow:hidden;
}

.foucrs div {
 float:left;
 margin-left:14px;
 margin-right:14px;
 padding:10px;
 border:1px solid rgb(101,154,86);;
 position:relative
}
.foucrs div .selecte{
  width:16px;
  height:16px;
  background:rgb(101,154,86);
  color:#fff;
  line-height:16px;
  text-align:center;
  border-radius:2px;
  position:absolute;
  right:0;
  bottom:0;
}


.upload{
margin-top:20px;
overflow:hidden;
}
.upload > span{
 float:left;

}
.upload > .upladelfet{
 float:left;
 width:80%;
 
}
.ImgArr{
  overflow:hidden;
}
 
.ImgArr ul li {
 float:left;
 
  width:20%;
  text-align:center;
  height:60px;
  margin-left:14px;
  margin-top:10px;
  overflow:hidden;

}
.ImgArr ul li img{
  width:100%;
  height:100%;
}
.Img{
 margin-left:14px;
  float:left;
 padding:10px;
  width:20%;
  margin-top:10px; 
  border:1px dashed rgb(188,188,188);
  text-align:center;
   height:60px;
}
.Img > span{
 display:block;
 font-size:12px;
}
.Img img{
  width:30%;
}
</style>


