<template lang="html">
<div class="To">
     <div class="toHeader">
        <div id="div">
      <iframe src="http://pic3.sonhoo.com/photos_upload/upload_uer.html?id=0&select_pic=24&chk_par1=sh14344&chk_par2=FDD52526C6E19E732F855F68E91C2859&xh=1&c_url=http://192.168.100.228:8030">
      </iframe>

       </div>
         <span v-on:click="TextChe()">只能上传 jpg , pig , gif</span>
       
     </div>
   <div class="toImg">
<div class="Imgchild" v-for="(item,index) in url" v-bind:id="[url[index].fn ? 'actives':'']">
   <div class="border">
       <img v-bind:src="'http://image.sonhoo.com/'+item.DataUrls">
     
       <span class="Ico">
       <i class="ivu-icon ivu-icon-close-round" v-on:click.stop="delet(index)"></i>
      <i class="ivu-icon ivu-icon-checkmark-round" v-on:click.stop="addgallery(index)"></i>
  
       </span>
   </div>  
   <div></div>
   </div>
   </div>  
</div>
</template>

<script>
	var i=0
 export default {
    props:"arrays",
    data(){
        return{
	   show:false,
	   imgName: '',
       visible: false,
	   defaultList:[],
	   uploadList:[],
       modal1: false,
     
       url:["数据"]
	}
    },
    methods:{
        TextChe(){
        	this.b++
        	 console.log(this.url)
           console.log(this.$store.state.CateGory.DataUrl)
        },
    	ok () {
    		 
    		  
                this.$Message.info('点击了确定');
            },
            cancel () {
                this.$Message.info('点击了取消');
            },
      
      delet:function(Index){
    
          this.defaultList.splice(Index,1)
       },
       addgallery:function(Index){
            if(this.defaultList[Index].fn!=true){
	       this.defaultList[Index].fn = true
	       
	       this.uploadList.push(this.defaultList[Index])
	       this.$emit("addgallery",this.uploadList)
	    }else{
	     
	        this.defaultList[Index].fn = false
	        this.$emit("DeleteImg",Index)
	    }
	 
         }
    },
    computed:{
    DataUrls(){
     //this.url = this.$store.state.CateGory.DataUrl
   
       this.url.push(this.$store.state.CateGory.DataUrl)
     
    return this.$store.state.CateGory.DataUrl   //获取连接数据
   
    }
 }
  
 }
</script>

<style scoped>
/*iframe css样式*/

/*++++++++++=========*/
.toHeader{
 overflow:hidden;
}
.toHeader > span,
.toHeader > div {
 float:left;
}
.toHeader > span{
 margin-left:14px;
 font-size:14px;
 line-height:40px;
 color:rgb(188,188,188)
}
.toImg{
  overflow:hidden;
}
.toImg .Imgchild{
  width:20%;
  float:left;
  margin-left:10px;
  position:relative;
  margin-top:10px;
  text-align:center;
}
.Imgchild .border:hover{
 border:1px solid rgb(45,141,15)
}
.Imgchild .border:hover > span{
  display:block
}
.Imgchild .border{
 border:1px solid #fff;
}

.toImg .Imgchild div img{
  width:100%;
  height:100%;
}
.border .Ico{
  display:block;
  width:100%;
  position:absolute;
  left:0;
  bottom:0px;
  text-align:center;
   color:#fff;
   background:rgb(45,141,15);
   display:none;
  }
.toImg #actives .border{

    border:1px solid rgb(45,141,15);
    
  }
.toImg #actives .border .Ico{
    display:block;
   
 } 

</style>



