<template lang="html">

     <div class="addPoints">
       <!--文本输入框-->
       <modelName :Textname="Textname[0]" :_index=[false,1]></modelName>
       <div class="Setup">
       
         <div class="elementSet">
          <h2>元素设置 :</h2>
	  <NameModel style="margin-left:38px;margin-top:20px;" v-on:accept="accept"></NameModel>
	  </div>
          <div class="elementIco">
	  <span>图标 :</span>
	  <div class="upload">
 <Radio-group v-model="animal">
 <Radio label="默认"></Radio></span>
 <Radio label="隐藏" style="margin-left:20px;"></Radio>
<Radio label="自定义(最佳尺寸80*80)" style="margin-left:20px;"></Radio>
    </Radio-group>

       <div class="windowupload" v-show="animal===compare[0].iconCompare">
         <div class="Icon">
	   <span><Icon type="ios-reverse-camera"></Icon></span>
	   </div>
	 <Button type="ghost" style="margin-left:14px;margin-top:4px;color:#7dc9fb;float:left">系统图标</Button>
	 <div style="margin-left:20px;margin-top:4px;float:left">
	 <Upload action="http://pic3.sonhoo.com/photos_upload/upload_uer.html?id=0&select_pic=24&chk_par1=sh14344&chk_par2=FDD52526C6E19E732F855F68E91C2859&xh=1&c_url=http://192.168.100.228:80/vpaging#/" methods="post" enctype="multipart/form-data">
	 <Button type="ghost" style="color:#7dc9fb;">上传图片</Button>
         </Upload>
	 </div>
     <div class="Scroll">
       <div class="Scroll_top">
                <!--系统图片-->
                <span @click="addImg(item.Pic,index)" v-for="(item,index) in SystemImg" ref="span">
          
                      <img v-bind:src=Url+item.Pic>
                </span>

         </div>

     </div>
      </div>

    </div>

	  </div>


   <div class="elmentColor clearfloat">
	  <span >颜色 :</span>
	  <div class="upload"style="position:relative;">
	
      	<Row>
      		<Col span="12">
      		<ColorPicker v-model="color1" size="small" @on-change="Col(color1)"/>
      		</Col>
      		
      	</Row>
     
             </div>
   </div>



<div class="seniorleft ">
 <h2>跳转设置 :</h2>
  <div class="basischild">
	   <span class="left">跳转至 : </span>
	   <div class="right">
	   <div>
	      <Radio-group v-model="JumpTo">
               <Radio label="自定义"></Radio>
                <Radio label="不跳转" style="margin-left:20px;"></Radio>
                </Radio-group>
		</div>
                <div class="right_Mar" v-show="JumpTo==compare[0].JumpTo">
	        <Input v-model="jump" placeholder="填入跳转的页面" style="width: 300px"></Input>
	        <Button type="ghost" v-on:click="addLink()">设置连接</Button>
	     </div>
		</div>
	   </div>
</div>
<div class="seniorleft" v-show="jump!=''">
	<h2>样式设置 :</h2>
	 <div class="basischild">
	   <span class="left">标题 : </span>
	      <div class="right">
	     <div>
	      <Radio-group v-model="setName">
               <Radio label="自定义"></Radio>
                <Radio label="默认" style="margin-left:20px;"></Radio>
                </Radio-group>
		 </div>
		<div v-show="setName!='默认'"> 
		 <div class="right_Mar" >
	        <div class="Size">
		<span>大小 : </span>
		  <Select v-model="model2" size="small" style="width:80px;">
                 <Option v-for="item in cityList" :value="item.value" :key="item">{{ item.label }}</Option>
              </Select>

		</div>
		   		<div class="botn" >
                 <Checkbox v-model="fruit">加粗</Checkbox>
                </div>
              
	     
		</div>
		<div class="ColorS">
	      <span>颜色 :</span>
	     
	      </div>
		</div>
	 </div>
</div>
</div>


       </div>
<div class="Button">
     <Button type="text" v-on:click.stop ="submit">确定</Button>
       <Button type="text" v-on:click="seed">取消</Button>
  </div>
<!--设置链接-->
<div class="setlink" v-show=secondary>

 <articlelinks></articlelinks>

</div>
  
     </div>


</template>

<script>
var Json={};
import modelName from "../../PublicModule/modelName"
import NameModel from "../../PublicModule/NameModel"
import articlelinks from "../../PublicModule/articlelinks"

import selector from "../../PublicModule/selector"
 export default {
 
 data(){
     return {
       Url:"http://image.sonhoo.com/",
        color1: '#19be6b',
       compare:[{
	   JumpTo:"自定义",
	    iconCompare:"自定义(最佳尺寸80*80)",
	   }
	 ],
     Textname:["添加分类导航链接"],
	 animal:'默认',
	 jump:"",
	 JumpTo:"不跳转",
	 model2:"",
	 fruit:false,
	 setName:"默认",
	 cityList:[
	 {label:"12px",value:"12px"},
	 {label:"13px",value:"13px"}
	 ,{label:"14px",value:"14px"}
	 ,{label:"15px",value:"15px"}
	 ,{label:"16px",value:"16px"}
          ],
   gather:{name:"标题",accept:false,url:"",icon:"#",color:"#666",fontColor:"",img:"默认",Size:"12px",fontWeight:"",hide:"显示",Accor:["checkmark-round","close-round",0]},
       }
  },

   methods:{
   	selectorColor:function(data){
      
   		this.gather.fontColor = data
   	   
   	},
   	//获取selector组件传递过来的颜色选择
      Col:function(data){
     
      	this.gather.color =data
      
      },
      seed:function(){   //关闭一级设置连接
       this.$store.dispatch("seed",[false,1])
     },
//传递的参数来改变二级状态
      addLink:function(){
        if(this.secondary===false){
	      this.$store.dispatch("secondary",[true,2])
	   }else{
	      this.$store.dispatch("secondary",[false,2])
	   }
      },

   /*这个事件是由NameModel来跟输入的数据模块触发*/
      accept:function(data,accept){
          this.gather.name = data
	      this.gather.accept = accept
      },
   //根据点击的图片来传给相同的数据
      addImg:function(item,index){
   
     for(var i=0;i<this.$refs.span.length;i++){
         this.$refs.span[i].style.border="1px solid #fff";
        }
       this.$refs.span[index].style.border="1px solid rgb(125,201,251)";
       this.gather.icon = this.Url+item
      
	
      },
  //这里要提交数据
  
    submit:function(){
      this.$emit("passEvent",this.gather)
      this.seed() //关闭
  }
   },

  computed:{
     // 获取二级状态值
     secondary(){
        return this.$store.state.CateGory.secondary
      },
      /*加载系统图片*/
     SystemImg(){
    	return this.$store.state.CateGory.SystemImg
    },
       // 拼接的连接
     connection(){
        return this.$store.state.CateGory.connection
       }

  },
 watch:{    //这里监听 connection是否改变了，如果改变就会复制给jump然后渲染到设置连接文本框
       connection:function(){
         
        this.jump = this.connection
	    this.gather.url =  this.connection  //获取当前的连接地址
       },
       model2:function(val){
         this.gather.Size = this.model2
       },
       fruit:function(){
       	 this.gather.fontWeight =this.fruit
       }

    },
   components:{
     modelName,
     NameModel,
     articlelinks,
     selector
     
   }

 }
</script>

<style scoped>

h2{
 font-size:16px;
}
  .addPoints{
    width:100%;
  background:#fff;
  font-size:14px;
  padding-bottom:60px;
  position:relative;

 }
  .Setup{
   padding:0 24px;
   margin-top:20px;
  }
    .elementIco{
    overflow:hidden;
    margin-top:20px;
    }

    .elmentColor span,
   .elementIco > span{
     float:left;
     margin-left:38px;
     margin-right:14px;
   }
  .elementSet h2{
   font-size:16px;
  }
.upload{
width:80%;
 float:left;
}
.windowupload{
 overflow:hidden;
 margin-top:20px;
}
.windowupload .Icon{
 font-size:24px;
 float:left;
width:40px;
padding:2px 2px;
border-radius:4px;
border:1px solid #000;
color:#7bbbb7;
text-align:center;
}
.windowupload .Icon span {
   background:#a2d7d4;
   display:block;
   border-radius:4px;

}
.elmentColor {
margin-top:20px;

}

 .seniorleft{
  overflow:hidden;
  margin-top:20px;
 }
 .seniorleft .left{
  float:left;
  margin-right:14px;
  margin-left:24px;
 }
 .seniorleft .right{
  float:left;
 }
 .right_Mar{margin-top:20px;overflow:hidden;}
 .Size{
  float:left;
   font-size:12px;

 }
 .Size span {
 margin-right:14px;
 }
 .botn{
  float:left;
  font-size:12px;
  margin-left:10px;
  margin-top:4px;
 }
 .ColorS{
  margin-top:20px;
   font-size:12px;
   overflow:hidden;
 }
 .ColorS span{
  float:left;
  margin-right:14px;
 }
 .ColorS i{
   width:30px;
   height:30px;
   float:left;
  margin-top:2px;
 }

.Button{
 position:absolute;
 right:20px;
 bottom:10px;
}

.setlink{
width:240px;
position:absolute;
top:0px;
right:-270px;
}
.Scroll{
  width:100%;
  height:210px;
  border:1px solid #dad8d9;
  margin-top:20px;
  position:relative;
      overflow-y: scroll
}
.Scroll_top{
   width:100%;
}

.Scroll_top span{
   float:left;
   width:20%;
   text-align:center;
   height:60px;
   border:1px solid #fff;
   padding:4px;
   overflow:hidden;
}
.Scroll_top span img{
	width:100%;
}
</style>



