<template>
    <div class="Color clearfix">
      <span class="In" BG="" style="background:#dddddd">默认</span>
      <span  BG="" v-for="(item,index) in Colors" v-bind:style="item" v-on:click.stop="Col(item.background)"></span>
     <div class="classifyColor">
      	<Row>
      		<Col span="12">
      		<ColorPicker v-model="color1" size="small" @on-change="Col(color1)"/>
      		</Col>
      		
      	</Row>
      	</div>
     
     
    </div>

</template>

<script>
import selector from "./selector"
export default {
   data(){
        return{
      color1: '#19be6b',
     color:"",
	  Colors:[
		{
		       background:"#666666"
		    },
		   {
		     background:"#1d1d1d"
		    },
		   {
		     background:"#bb140c"
		    },
		    {
		     background:"#ff692c"
		    },
		    {
		     background:"#069f5c"
		    },
		    {
		     background:"#65bcc5"
		    },
		    {
		     background:"#0657a9"
		    },
		    {
		     background:"#d755ea"
		    },
		     {
		     background:"#7dc9fb"
		    }

		]
	}
      },
      methods:{
   
      Col:function(Index){
    
  
       this.$emit("Module",Index)   //传递给BtnNav模块
      }
    },
    components:{
      selector
    }

}
</script>

<style scoped>
	.classifyColor{
		float:left;
		height:21px;
		z-index:9999999;
		margin-left:8px;
		border:none;
	}

	.Color{
		position: relative;
	}
.Color .In{
 width: auto;
 padding:0px 4px;
 height:24px;
float:left;
}
.Color span {
  display:inline-block;
  width:24px;
  height:24px;
  margin-left:8px;
  font-size:14px;
 float:left;
}

</style>



