<template lang="html">

   <!--底部导航-->
   <div class="ifica_action">
<modelName v-bind:Textname="Textname"></modelName>

<!--常规-->
<div class="twoactive">
   <span v-on:click="switchs(true)" v-bind:class="[show?'active':'']">常规</span><span v-on:click="switchs(false)" v-bind:class="[show!=true?'active':'']">高级</span>
</div>
<!--切换-->
<div class="twolable1" v-show="show!=false">
     <div>
        <span>搜索提示 : </span> 
	<Input v-model="value" placeholder="请输入关键词" style="width: 250px"></Input>
     </div>

     <div class="over">
        <span>热门搜索 : </span> 
	<div>
	   <span>热门热门</span>
	   <span>热门热门</span>
	   <span>热门热门</span>
           <span>热门热门</span>
	   <span>热门热门</span>
	   <span>热门热门</span>
	   <span>热门热门</span>
	   <span>热门热门</span>
	   <span>热门热门</span>
	   <span>热门热门</span>
	   <span>热门热门</span>


	</div>
     </div>


</div>

<div class="twolable2" v-show="show!=true">
     <div>
        <span>搜索提示 : </span>
     </div>
</div>
<div class="Button">
     <Button type="text">确定</Button>
       <Button type="text">取消</Button>
  </div>
   </div>
 
</template>

<script>

import styleColor from '../PublicModule/styleColor'
import modelName from "../PublicModule/modelName"
 export default {
     data(){
     return{
 value: '',
 show:true,
 Textname:"底部导航模块"
            }
   },
 methods:{
switchs:function(fn){
this.show = fn;
}
},
components:{
	 styleColor,
	 modelName
	}
   

 }
</script>

<style scoped>

.ifica_action{
 width:50%;
  background:#fff;
  margin:0 auto;
  margin-top:48px;
  padding-bottom:60px;
  position:relative;
  
}




.rights{
 float:right;
}

.twoactive{
  padding:0 24px;
  background:#e6e6e6;
  font-size:12px;
}
.twoactive > span{
  height:100%;
  padding:0 8px;
  display:inline-block;
}


.twolable,
.twolable2,
.twolable1{
margin-top:30px;
padding:0 24px;
 font-size:14px;
 position:relative;
 padding-bottom:60px;
}
.twolable1 > div > span{
  margin-right:10px;
}
.over{
margin-top:30px;
 overflow:hidden;
}
.over > span{
 float:left;
 
 
}
.over div{
width:80%;
 float:left;
 overflow:hidden;
}
.over div span{
  padding:2px 4px;
  color:#fff;
  display:block;
  float:left;
  background:#ffb399;
  margin-right:20px;
  margin-bottom:10px;
}

.Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
.ivu-btn-dashed{
 border:0;
}
.active{
  background:#fff;
  color:#565656;
}
</style>



