<template>
    <div class="Mfooters">

     <div class="ico1" v-if='classify[classify.length-1]._index==1' v-bind:style="{color:classify[classify.length-1].colors,borderColor:classify[classify.length-1].colors}" >
	   <a v-bind:src=item.url  v-for="(item,index) in classify" v-bind:style="{color:classify[classify.length-1].colors,width:100/(classify.length-1)+'%'}" v-if="!item._index">
	   <Icon type="ios-home-outline"></Icon></i>
	   <p v-text="item.name"></p>
	   </a>
	</div>

	<div class="ico2" v-if='classify[classify.length-1]._index==2'  v-bind:style="{borderColor:classify[classify.length-1].colors,background:classify[classify.length-1].colors}">
	 <a v-bind:src=item.url  v-for="(item,index) in classify" style="{color:#fff}" v-bind:style="{width:100/(classify.length-1)+'%'}" v-if="!item._index">
	   <Icon type="ios-home-outline"></Icon></i>
	   <p v-text="item.name"></p>
	   </a>
          </div>

   </div>
</template>

<script>
export default {
data(){
     return{
   
     }
 },
 computed:{
 	classify(){
 		console.log(this.$store.state.moduleElement.footer)
 		return this.$store.state.moduleElement.footer;
 	}
 }


}
</script>

<style>
.Mfooters{
width:100%;
 background:#fff;
 overflow:hidden;
 bottom:0px;
}
.ico2,
.ico1{
    width:100%;
    border:1px solid rgb(17,150,219);
    float:left;

    font-size:14px;
    overflow:hidden;
    padding:2px 0;
  }
 .ico2 a,
.ico1 a{
   float:left;
  }
.ico1 a{
 
     text-align:center;
  }
.ico2 {
  background: rgb(17,150,219);
 }
.ico2 a{
   
    text-align:center;
    color:#fff;
  }
</style>



