<template>
    <div class="search">
       <div class="searc-wrap">
       <div class="in" v-if ="serach.indexs <=2" v-bind:class="'in'+serach.indexs">
       	
        <input type="text" placeholder="Sercar" v-bind:style={background:serach.colors}>
	<Icon type="android-search" class="Icons"></Icon>
      </div>
      
      <div class="in" v-if ="serach.indexs > 2 && serach.indexs <=4" v-bind:class="'in'+serach.indexs">
      	
        <input type="text" placeholder="Sercar" v-bind:style={borderColor:serach.colors}>
	<Icon type="android-search" class="Icons" v-bind:style={background:serach.colors}></Icon>
      </div>
      
   </div>
   </div>
</template>

<script>
export default {
    computed:{
      serach(){
       console.log(this.$store.state.moduleElement.search)
       return this.$store.state.moduleElement.search
      }
    } 
}
</script>

<style scoped>

input::input-placeholder{
 color:#fff;
}
input::-webkit-input-placeholder{
 color:#fff;
}
input::-moz-input-placeholder{
 color:#fff;
}
input::-o-input-placeholder{
 color:#fff;
}
.searc-wrap{
  width:86%;
  margin:0 auto;
   padding:10px 40px 10px 20px;;
   position:relative;
   
}
.active{
  border:1px solid #1296db;
}

.wrapInc{
  position:absolute;
  width:24px;
  height:24px;
  border-radius:2px;
  color:#fff;
  background:#1296db;
  line-height:24px;
  text-align:center;
 right:0;
 bottom:0;
 display:none;
}

.search{

  overflow:hidden;


}
.in{
   width:100%;
   position:relative;
  
   
}
.in input{
  width:100%;
  padding:4px 20px;
  border-radius:24px;
  border:0;
  background:#1296db;
  color:#fff;
}

.in1 input{
  border-radius:2px;
}
.in3 input {
 background:none;
 border:1px solid #1296db;
 color:#1296db;
 border-radius:0;
 outline:none;


}


input.ic::input-placeholder{
 color:#1296db;
}
input.ic::-webkit-input-placeholder{
 color:#1296db;
}
input.ic::-moz-input-placeholder{
 color:#1296db;
}
.input.ic::-o-input-placeholder{
 color:#1296db;
}
input.ic::-ms-input-placeholder{
 color:#1296db;
}

.in4 input{
 background:none;
  border:1px solid #1296db;
  border-radius:12px;
  color:#1296db;
}

input:focus { outline: none; } 

.Icons{
   position:absolute;
   right:10px;
   top:8px;
   color:#fff;

}
.in3 > .Icons{
top:0px;
  width:31px;
  right:0;
  height:28px;
  background:#1296db;
  text-align:center;
  line-height:28px;
}

.in4 > .Icons {
  width:24px;
  height:24px;
  border-radius:24px;
  background:#1296db;
  color:#fff;
  text-align:center;
  line-height:24px;
  right:4px;
  top:2px;
  
  
}
</style>



