import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios"
Vue.prototype.axios = axios
Vue.use(Vuex)


//加载手机代码各个模块

const moduleElement = {
	  state:{
	  	menu:"",
	  	footer:"",
	  	product:"",
	  	Settings:"",
	  	search:"",
	  	carousel:"",
	  	articeList:"",   //文章存储的数据
	  	setvalue:"",  //文章的设置
	  	announcement:"",//公告通知
	  	photos:""
	  },

	  
	 actions:{
	 	//多图展示
	 	photos({commit},photos){
	 		console.log(photos)
	 			commit("photos",photos)
	 	},
	 	//公告通知
	 	announcement({commit},announcement){
	 			commit("announcement",announcement)
	 	},
	 	//文章列表数据添加
	 	articeList({commit},articeList){
	 		 console.log(articeList)
	 		commit("articeList",articeList)
	 	},
	 		  //多图滚动图片待添加事件
	 carousel({commit},carousel){
	 		commit("carousel",carousel)
	 },
	 	 //search搜索模块数据
	 	 search({commit},search){
	 	 	commit("search",search)
	 	 },
	 	
	 	//产品展示高级设置信息
	 	Settings({commit},Settings){
	 		commit("Settings",Settings)
	 	},
	 	//产品展示数据
	 	product({commit},product){
	 		  commit("product",product)
	 	},
	 // 分类导航模块数据	
     menu({commit},menu){
		
	    commit("menu",menu)
	 },
	 //底部导航
	   footer({commit},footer){
	   	commit("footer",footer)
	   },
	  },
  mutations:{
  	photos:function(state,photos){

  		state.photos = photos;
  	},
  	announcement:function(state,announcement){
  	
  		state.announcement = announcement;
  	},
  	articeList:function(state,articeList){
  		console.log(articeList)
  		state.articeList = articeList[0]
  	  state.setvalue   = articeList[1]
  	},
  		carousel:function(state,carousel){

  		state.carousel = carousel
  	},
  	search:function(state,search){
  		state.search = search
  	},
  	Settings:function(state,Settings){
  		state.Settings = Settings
  	},
  	product:function(state,product){
  	
  		state.product = product;
  	},
	  menu:function(state,menu){
	  state.menu = menu
	 },
	 footer:function(state,footer){
	 	state.footer = footer
	 }
	 }
}
// 用户登录
const UserName = {
	state:{
		userName:""
	},
	actions:{
	userName({commit}){
		axios.get("/test/api.aspx?method=wx.login.state&uid=9")
   	.then( (res)=>{
   		
           console.log(res.data)
   	    commit("userName",res.data)      
   	})
   	.catch(function (error) {
           console.log(error);
                  });
},
	},
	mutations:{
  userName:function(state,userName){
	
    state.userName = userName
   
  },
	}
}
/// 分类导航模块
const CateGory = {
  state:{
    father:false,   //控制父级隐藏显示
    seed:false,     //控制子级隐藏显示
    seed_index:"",   //修改分类导航点击的数据，根据当前的值
    secondary:false, //控制二级显示隐藏，如连接设置
    connection:"" ,  //路径连接
    bgImg:"",
    ProData:"",  //产品分类
    article:"", //文章列表
    SystemImg:"", //系统默认图片
    UserImage:"",  //用户上传的图片
    DataUrl:"",     //传递图片的连接路径
    Theme:"",       //风格主题
  },

actions:{
	// 风格主题
Theme({commit}){
		axios.get("/test/")
   	.then( (res)=>{

   	    commit("Theme",res.data.list.listinfo)      
   	})
   	.catch(function (error) {
           console.log(error);
                  });
},
		
		// 传递图片的连接路径
DataUrl({commit},DataUrl){
	 if(DataUrl===""){
	  	commit("DataUrl",2222)      
	  }else{
   	    commit("DataUrl",DataUrl)      
  }
},
	// 用户图片
UserImage({commit}){
		axios.get("/test/api.aspx?method=wx.get.uppic&uid=9&page_no=1&page_size=20")
   	.then( (res)=>{

   	    commit("UserImage",res.data.list.listinfo)      
   	})
   	.catch(function (error) {
           console.log(error);
                  });
},
	// 系统默认图片
SystemImg({commit}){
		axios.get("/test/api.aspx?method=wx.get.pic")
   	.then( (res)=>{

   	    commit("SystemImg",res.data.list.listinfo)      
   	})
   	.catch(function (error) {
           console.log(error);
                  });
},
	  // 文章列表
article({commit}){
	  	
	axios.get("/test/api.aspx?method=wx.get.cormenu&uid=9")
   	.then( (res)=>{
   	
   	    commit("article",res.data.list.listinfo)      
   	})
   	.catch(function (error) {
           console.log(error);
                  });
            
	  },	
	  // 产品分类数据
ProData({commit}){
	  	
	axios.get("/test/api.aspx?method=wx.get.class&uid=9")
   	.then( (res)=>{
   	
   	    commit("ProData",res.data.list.listinfo)      
   	})
   	.catch(function (error) {
           console.log(error);
                  });
            
	  },
		//背景获取的数据
     bgImg({commit},bgImg){
        commit("bgImg",bgImg)
	  
	 },
      //父级控制
   father({commit},_index){
	    commit("father",_index)
	 },
   //子级控制
    seed({commit},_index){
		
	    commit("seed",_index)
	 },
   //二级控制
     secondary({commit},_index){

      commit('secondary',_index)

    },
   //路径连接
  connection({commit},url){
      commit("connection",url)
  }
  },

mutations: {
		// 获取用户上传图片的参数
	DataUrl:function(state,DataUrl){
	    state.DataUrl = DataUrl
     },
	// 用户图片
	UserImage:function(state,UserImage){
		state.UserImage = UserImage
	},
	// 系统默认图片
	SystemImg:function(state,SystemImg){
		state.SystemImg = SystemImg;
	},
		//产品分类数据
	article:function(state,article){
		console.log(article)
	    state.article = article;
	 },
	 	//产品分类数据
	ProData:function(state,ProData){
	    state.ProData = ProData;
	 },
	 bgImg:function(state,Bg){
	   state.bgImg = Bg
		 
  },
  //背景图片
	 bgImg:function(state,Bg){
	   state.bgImg = Bg
		 
  },

	//父级控制
	father:function(state,_index){
	   state.father = _index[0]
	},
  //子级控制
  seed:function(state,_index){
	 
    state.seed = _index[0]
    state.seed_index = _index[2]    //接受当前是否修改
  },

 //根据传递的参数来改变二级状态
   secondary:function(state,_index){

       state.secondary = _index[0]
    },
    //改变文章链接
   connection:function(state,url){
	  
       state.connection = url
		  
    }
   }
}
const store = new Vuex.Store({
 modules:{
   CateGory:CateGory,
   UserName:UserName,
   moduleElement:moduleElement
 }

})


export default store



