<template lang="html">
   
    <div class="addannouncement">
       <modelName :Textname="Textname" v-on:hideLink="hideLink" ></modelName>
       <div class="contentadd">
          <div class="top">
	  <span style="margin-right:6px">公告内容 : </span>
	   <Input v-model="value" placeholder="请输入..." style="width: 170px"></Input>
	  </div>
	  <div class="clears">
	    <span style="margin-right:6px">公告连接 :</span>
	   <div class="radis">
	          <div>
		       <Radio-group v-model="animal">
        <Radio label="自定义"></Radio>
        <Radio label="无"></Radio>
      
    </Radio-group>
		  </div>
		  <div style="margin-top:20px" v-show="animal===animal1">
		    <Input v-model="connection" placeholder="填入跳转的页面" style="width: 90px"></Input>
	        <Button type="ghost" v-on:click="addLink()">设置连接</Button>
		  </div>
	   </div>
	  </div>
       </div>
       <div class="Button">
     <Button type="text" v-on:click.stop="submit">确定</Button>
       <Button type="text" v-on:click="hideLink">取消</Button>
  </div>
  
  <!--设置链接-->
<div class="setlink" v-show=secondary>

 <articlelinks></articlelinks>

</div>
  
    </div>
   
</template>

<script>

import modelName from "../../PublicModule/modelName"
import NameModel from "../../PublicModule/NameModel"
import articlelinks from "../../PublicModule/articlelinks"
 export default {
 	props:["index"],
 data(){
     return {
       Textname:"添加公告",
       value:"",
    
       animal:"自定义",
       animal1:"自定义",
       connections:"",
       Json:{"name":"","url":""}
       }
   },
   methods:{

      hideLink:function(){
   
        this.$emit("hideLink",false)
      },
      //传递的参数来改变二级状态
      addLink:function(){
      
        if(this.secondary===false){
	      this.$store.dispatch("secondary",[true,2])
	   }else{
	      this.$store.dispatch("secondary",[false,2])
	   }
      },
      submit:function(){
        this.Json.name=this.value;
	  this.Json.url = this.connection;
	  if(this.index>=0){
	
	  	 this.$emit("amend",this.Json,this.index)
	  }else{
	 this.$emit("addEvent",this.Json)
	 }
	 this.hideLink();
      }
   },
     computed:{
    
      // 获取二级状态值
     secondary(){
     	
        return this.$store.state.CateGory.secondary
      },
       // 拼接的连接
     connection(){
        return this.$store.state.CateGory.connection
       }

  },
   watch:{    //这里监听 connection是否改变了，如果改变就会复制给jump然后渲染到设置连接文本框
       connections:function(){
         
     
	    this.gather.url =  this.connections  //获取当前的连接地址
       }
     },
   components:{
     modelName,
     NameModel,
     articlelinks
   }

 }
</script>

<style scoped>
	.setlink{
width:240px;
position:absolute;
top:260px;
right:60px;
}
.addannouncement{
 
}
.contentadd{
  padding:0 24px;
  font-size:14px;
}
.top,
.clears{
overflow:hidden;
margin-top:20px;
}
.clears > div,
.clears > span{
 float:left
}
.clears > div{
 width:70%;
}

 .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
</style>



