
<template>
    <div class="layout">


        <div class="layout-ceiling">
          <div class="layout-ceiling-Left">
          	<a href="#" class="NameUsers" v-if="UserName.response_code===10000">
      <img v-bind:src=Src+UserName.cimg />
                  <i v-text=UserName.username></i>
                </a>
          <a class="NumeUsers" v-if="UserName.response_code===10003"><i>请登录</i></a>
              <a style="margin-left: 36px">商虎助手</a>
              <Dropdown style="margin-left: 36px">
        <a href="javascript:void(0)">
            在线咨询
            <Icon type="arrow-down-b"></Icon>
        </a>
        <Dropdown-menu slot="list">
            <Dropdown-item>驴打滚</Dropdown-item>
            <Dropdown-item>炸酱面</Dropdown-item>
            <Dropdown-item>豆汁儿</Dropdown-item>
            <Dropdown-item>冰糖葫芦</Dropdown-item>
            <Dropdown-item>北京烤鸭</Dropdown-item>
        </Dropdown-menu>
    </Dropdown>

          </div>
            <div class="layout-ceiling-main">
                <a href="#" v-on:click.stop="save"><i></i>保存</a>
                <a href="#">取消</a>
                <a href="#">预览</a>

            </div>
        </div>

    </div>
</template>
<script>
const ERR_OK = 0;
    export default {
       data(){
         return{
           rows:[],
           Src:"http://image.sonhoo.com/",
           account:""
         }
       },
    created(){
   	 this.$store.dispatch("userName")
   },
       methods:{
          save:function(){
	  this.$root.eventHub.$emit('eventName', event.target)
	  }
       },
       computed:{
    UserName(){
     //this.url = this.$store.state.CateGory.DataUrl
   
  console.log(this.$store.state.UserName.userName)
    return this.$store.state.UserName.userName   //获取用户登录状态
   
    }
 }
    }
</script>


<style>
    .layout{
        border: 1px solid #d7dde4;
        background: #7dc9fb;
        position: relative;
        border-radius: 4px;
        padding:0 42px 0 21px;
    }

    .layout-header{
        height: 60px;
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .layout-copy{
        text-align: center;
        padding: 10px 0 20px;
        color: #9ea7b4;
    }
    .layout-ceiling{
        background: #7dc9fb;
        padding: 8px 0;
        overflow: hidden;

    }
    .layout-ceiling-Left{
        float: left;
        margin-right: 15px;
    }
  .layout-ceiling-main{
        float: right;
        margin-right: 15px;
    }
    .layout-ceiling-Left a,
    .layout-ceiling-main a{
      display: inline-block;
        color: #fff;
        font-size:14px;
       vertical-align: text-top;
      line-height:40px;
    }
    .layout-ceiling-main a + a {
      margin-left:36px;

    }



  .NameUsers img{
    width:25px;
    height:25px;
    border-radius:25px;
    position: relative;
    top:8px;
}

  .NameUsers i{
    font-style: normal;
    vertical-align:top;
  }
</style>



