<template lang="html">
  <div class="pare">
        <modelName :Textname="Textname[0]" :_index=[false,2]></modelName>
        <div class="child">
          <span>链接类型 : </span>
        <Select v-model="model4" size="small" style="width:120px;">
                   <Option v-for="(item,index) in article" :label="item.bigclassname" :value="item.bigclassname" :key="item.url"></Option>
          </Select></div>
        <div class="child"><span>文章类型 : </span>
        <Select v-model="model5" size="small" style="width:120px;" >
                   <Option v-for="(item,index) in article" :label="item.bigclassname"  :value="item.bigclassname" :key="item.url"></Option>
                </Select>
      </div>
  <div class="Button">
       <Button type="text" v-on:click="addEvent">确定</Button>
         <Button type="text" v-on:click="secondary">取消</Button>
    </div>
        </div>
</template>

<script>
import modelName from "./modelName"
export default {
data(){
  return{
     articles:"",//连接类型
     Linktype:"",//文章类型
     Textname:["设置链接"],
     model4:"",
     model5:""
  }
},
  created(){
     this.$store.dispatch("article")   // 页面加载触发文章列表事件获取数据
},
computed: {
	 article:function(){
	 	
    return this.$store.state.CateGory.article  //获取文章列表分类数据
     }
},
methods:{

   secondary:function(){   //关闭设置连接
       this.$store.dispatch("secondary",[false,2])
   },
   addEvent:function(){  //添加设置连接选项
       var Url = this.model4+""+this.model5 //注意这里需要获取当前选中的value.url路径拼接
       this.$store.dispatch("connection",Url)
       this.secondary()
    }
},
components:{
    modelName
  }


}

</script>

<style scoped>
.pare{
 position:relative;
 background:#fff;
 font-size:14px;
 width:100%;
padding-bottom:60px;
}
.child{
 padding:0 24px;
 margin-top:20px;
}
   .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}

</style>



